<template>
  <div class="sz-module safeEmerge">
    <div class="sz-module-title">
       安全呈现
       <div class = 'safeEmerge-title-change' @click="contentchange" @mouseenter="isShow2=true" @mouseleave="isShow2=false">
         <i class="fas fa-sync-alt"></i>
         <div class="tooltipbox" v-show="isShow2">{{ tooltiptext }}</div>
       </div>
    </div>
    <div class = "sz-module-center">
      <div class="toplegend">
        <div class="threatlegend">
          <div class="ltitle">威胁事件总数:</div>
          <div class="datanum"><tooltip :val="threatnum">{{ threatnum }}</tooltip></div>
          <div class="unit">件</div>
        </div>
        <div class="underfirelegend">
          <div class="ltitle">受攻击资产总数:</div>
          <div class="datanum"><tooltip :val="underfirenum">{{ underfirenum }}</tooltip></div>
          <div class="unit">个</div>
        </div>
        <div class="aggressorlegend">
          <div class="ltitle">攻击者总数:</div>
          <div class="datanum"><tooltip :val="aggressornum">{{ aggressornum }}</tooltip></div>
          <div class="unit">位</div>
        </div>
        <div class="newaddlegend">
          <div class="ltitle">今日新增受攻击资产数:</div>
          <div class="datanum"><tooltip :val="newaddnum">{{ newaddnum }}</tooltip></div>
          <div class="unit">个</div>
        </div>
      </div>
      <div class="bottomlegend">
        <div class="threatevent" v-show="isShow==false">
          <div class="text">
            <div>攻击者</div>
            <div>受攻击资产</div>
          </div>
          <ul>
            <li v-for="(item, index) in threatevent" :key="index" class="addcolor" @click="check(index)">
              <span class="colorbox" :style="{'background-color': bgcolors[index],'border-bottom-color': bordercolors[index]}"></span>
              {{ item }}
              <span class="checkbox"></span>
              </li>
          </ul>
        </div>
        <div class="threatintelligence" v-show="isShow==true">
          <div class="text">
            <div>威胁类型</div>
          </div>
          <ul>
            <li v-for="(item, index) in threatintelligence" :key="index" class="addcolor" @click="check2(index)">
              <span class="colorbox" :style="{'background-color': colors2[index]}"></span>
              {{ item }}
              <span class="checkbox"></span>
              </li>
          </ul>
        </div>
      </div>
      <situation-map
       :tWidth='tparam.tWidth'
       :tShow='checkobject'
       :tHeight='tparam.tHeight'>
      </situation-map>
    </div>
  </div>
</template>

<script>
import { index } from '@/api'
import situationMap from '@/components/modules/situation-map'
import tooltip from '@/components/charts/svt-common/tooltip.vue'
import $ from 'jquery'
export default {
  name: 'safeEmerge',
  components: {
    situationMap,
    tooltip
  },
  props: {},
  data () {
    return {
      // 传递地图模块参数
      tparam: {
        tWidth: 1102,
        tHeight: 639,
        tShow: {
          'event': {
            '拒绝服务攻击': true,
            '勒索': true,
            '威胁网络流量': true,
            '蠕虫': true,
            '远控': true,
            '漏洞利用': true,
            '后门': true,
            '高级持续威胁': true
          }
        }
      },
      // 组件所有配置项
      options: {
        // 左右间距，预留出空间写字
        margin: 20,
        // 填充颜色
        color: ['rgba(234, 118, 103, .33)', 'rgba(234, 117, 103, .2)', 'rgba(234, 117, 102, .26)'],
        // 文字样式
        font: {
          // 文字大小
          size: 14,
          // 文字字体
          style: '微软雅黑',
          // 文字颜色
          color: '#fff'
        },
        // 延伸线样式
        line: {
          // 线粗细
          size: 1,
          // 线颜色
          color: '#d0feff'
        }
      },
      // 组件显示高度
      height: 250,
      // 组件显示宽度
      width: 390,
      // 组件应用数据
      data: [],
      total: '',
      monthAdd: '',
      level: [],
      // data: {},
      isShow: true,
      tooltiptext: '威胁情报',
      isShow2: false,
      threatevent: ['C&C', '扫描IP', '漏洞攻击IP', '肉鸡', '受扫描攻击资产', '受漏洞攻击资产'],
      bordercolors: ['#fc76c8', '#62d2fd', '#61ffc5', '#fc605c', '#ff995c', '#fedd6d'],
      bgcolors: ['transparent', 'transparent', 'transparent', '#fc605c', '#ff995c', '#fedd6d'],
      threatintelligence: ['拒绝服务攻击', '勒索', '威胁网络流量', '蠕虫', '远控', '漏洞利用', '后门', '高级持续威胁'],
      colors2: ['#cb3221', '#ffff1a', '#62ffdb', '#8635ff', '#ff9b1a', '#96ff1a', '#6acdff', '#ff73f3'],
      checkkey: 'event',
      checkvalue: {
        '拒绝服务攻击': true,
        '勒索': true,
        '威胁网络流量': true,
        '蠕虫': true,
        '远控': true,
        '漏洞利用': true,
        '后门': true,
        '高级持续威胁': true
      },
      checkvalue2: {
        'C&C': true,
        '扫描IP': true,
        '漏洞攻击IP': true,
        '肉鸡': true,
        '受扫描攻击资产': true,
        '受漏洞攻击资产': true
      },
      checkvalue1: {
        '拒绝服务攻击': true,
        '勒索': true,
        '威胁网络流量': true,
        '蠕虫': true,
        '远控': true,
        '漏洞利用': true,
        '后门': true,
        '高级持续威胁': true
      },
      checkobject: {},
      threatnum: '',
      underfirenum: '',
      aggressornum: '',
      newaddnum: ''
    }
  },
  // 计算变量
  computed: {
     // 全局时间轴选中的时间
    time () {
      return this.$store.state.time
    }
  },
  // 方法
  methods: {
    contentchange () {
      this.isShow = !this.isShow
      if (this.tooltiptext === '威胁事件') {
        this.tooltiptext = '威胁情报'
        this.checkobject = {}
        this.checkkey = 'event'
        this.checkvalue = this.checkvalue1
        this.checkobject[this.checkkey] = this.checkvalue
      } else {
        this.tooltiptext = '威胁事件'
        this.checkobject = {}
        this.checkkey = 'info'
        this.checkvalue = this.checkvalue2
        this.checkobject[this.checkkey] = this.checkvalue
      }
      // console.log(this.checkobject)
    },
    check (index) {
      let checkEleParent = $('.threatevent ul li:eq(' + index + ')')
      let checkEle = $('.threatevent ul li:eq(' + index + ') span:eq(1)')
      if (checkEle.hasClass('checkbox')) {
        checkEle.removeClass('checkbox')
        checkEleParent.removeClass('addcolor')
        for (let i = 0; i < Object.keys(this.checkvalue).length; i++) {
          let a = Object.keys(this.checkvalue)
          this.checkvalue[a[index]] = false
          this.checkvalue1[a[index]] = false
          this.checkvalue2[a[index]] = false
        }
      } else {
        checkEle.addClass('checkbox')
        checkEleParent.addClass('addcolor')
        for (let i = 0; i < Object.keys(this.checkvalue).length; i++) {
          let a = Object.keys(this.checkvalue)
          this.checkvalue[a[index]] = true
          this.checkvalue1[a[index]] = true
          this.checkvalue2[a[index]] = true
        }
      }
      this.checkobject = {}
      this.checkobject[this.checkkey] = this.checkvalue
      // console.log(this.checkobject)
      // console.log(this.checkobject[this.checkkey])
    },
    check2 (index) {
      let checkEleParent = $('.threatintelligence ul li:eq(' + index + ')')
      let checkEle = $('.threatintelligence ul li:eq(' + index + ') span:eq(1)')
      if (checkEle.hasClass('checkbox')) {
        checkEle.removeClass('checkbox')
        checkEleParent.removeClass('addcolor')
        for (let i = 0; i < Object.keys(this.checkvalue).length; i++) {
          let a = Object.keys(this.checkvalue)
          this.checkvalue[a[index]] = false
          this.checkvalue1[a[index]] = false
          this.checkvalue2[a[index]] = false
        }
      } else {
        checkEle.addClass('checkbox')
        checkEleParent.addClass('addcolor')
        for (let i = 0; i < Object.keys(this.checkvalue).length; i++) {
          let a = Object.keys(this.checkvalue)
          this.checkvalue[a[index]] = true
          this.checkvalue1[a[index]] = true
          this.checkvalue2[a[index]] = true
        }
      }
      this.checkobject = {}
      this.checkobject[this.checkkey] = this.checkvalue
      // console.log(this.checkobject)
      // console.log(this.checkobject[this.checkkey])
    },
    getTotal (params) {
       let promise = index.getSafeEmergeTotal(params)
        promise.then((res) => {
          if (Object.keys(res.data.data).length > 0) {
            let data = res.data.data
            this.threatnum = data.threat_event
            this.underfirenum = data.under_fire_asset
            this.aggressornum = data.aggressor
            this.newaddnum = data.newly_increase
          }
        }).catch(err => console.error(err))
    }
  },
  // 监听
  watch: {
     time (val, oldval) {
      let params = {time: this.time}
      this.getTotal(params)
    }
  },
  // 更新之后执行
  updated () {
    this.isLoad = false
  },
  mounted () {
    let params = {time: this.time}
    this.getTotal(params)
    this.checkobject[this.checkkey] = this.checkvalue
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.safeEmerge{
  width: 1110px;
  height: 679px;
  top: 101px;
  left:15px;
  padding: 4px 4px;
  background: url("~@/assets/images/safeEmerge/bg.png");
  background-color: transparent;
}
.safeEmerge-title-change{
  width: 15px;
  height: 100%;
  margin-right: 18px;
  float: right;
  color: #fff;
  cursor: pointer;
  position: relative;
}
.safeEmerge-title-change .tooltipbox{
  position: absolute;
  left:-60px;
  top:20px;
  width: 65px;
  height: 21px;
  line-height: 21px;
  text-align: center;
  border: 1px solid rgba(65,114,162,0.5);
  font-family: FZLTHJW;
  font-size: 12px;
  color: #ffffff;
  background-color: rgba(6,13,29,0.5);
  border-radius: 2px;
}
.toplegend{
  width: 534px;
  /* height: 55px; */
  /* border: 1px solid red; */
  position: absolute;
  left:20px;
  top:53px;
  font-family: FZLTHJW;
  z-index: 2;
  overflow: hidden;
}
.toplegend>div{
  float: left;
}
.threatlegend,.underfirelegend,.aggressorlegend,.newaddlegend{
  /* border: 1px solid red; */
  color: white;
  font-family: FZLTHJW;
  font-size: 14px;
  overflow: hidden;
  width: 210px;
  position: relative;
  height: 30px;
}
.underfirelegend{
  width: 224px;
  margin-left: 98px;
}
.toplegend .underfirelegend .datanum{
  color: #ff6c00;
}
.aggressorlegend{
  margin-top: 5px;
}
.toplegend .aggressorlegend .datanum{
  width: 114px;
}
.toplegend .newaddlegend {
  width: 224px;
  margin-left: 98px;
  margin-top:5px;
}
.toplegend .newaddlegend .datanum{
  width: 58px;
  color: #ff6c00;
}
.toplegend>div>div{
  float: left;
}
.toplegend .ltitle{
  padding-top: 10px;
}
.toplegend .datanum{
  font-size: 30px;
  color: #ffde00;
  height: 30px;
  line-height: 30px;
  width: 100px;
  /* border: 1px solid red; */
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-family: Letsgo;
}
.toplegend .unit{
  position: absolute;
  bottom: 3px;
  right: 0px;
  font-family: 'Microsoft YaHei';
}
.safeEmerge-title-change{
  cursor: pointer;
}
.bottomlegend{
  /* border: 1px solid red; */
  width: 596px;
  height: 64px;
  position: absolute;
  left:20px;
  bottom:20px;
  z-index: 2;
}
.bottomlegend>div{
  position: absolute;
  left:0;
  top:0;
  width: 596px;
  height: 54px;
}
.threatevent{
  /* border: 1px solid red; */
}
.threatintelligence{
  /* border: 1px solid green; */
}
.tooltipbox{
  display: block
}
.threatevent .text,.threatintelligence .text{
  font-size: 12px;
  font-family: FZLTHJW;
  text-align: right;
  color: #ffffff;
  width: 70px;
  position: absolute;
  top:0;
  left:0;
  /* border: 1px solid red; */
  height: 54px;
}
.threatintelligence .text{
  width: 59px;
  border-right: 1px solid #ffffff;
  padding-right: 10px;
  line-height: 54px;
}
.threatevent .text>div{
  width: 70px;
  border-right: 1px solid #ffffff;
  position: absolute;
  height: 22px;
  line-height: 22px;
  padding-right: 10px;
}
.threatevent .text>div:nth-child(1) {
  top:0px;
  right:0px;
}
.threatevent .text>div:nth-child(2) {
  bottom:0px;
  right:0px;
}
.threatevent ul,.threatintelligence ul{
  list-style-type: none;
  position: absolute;
  top:0;
  left:71px;
  width: 484px;
  /* border: 1px solid red; */
  margin: 0;
  padding: 0;
  text-align: left;
}
.threatintelligence ul{
  width: 536px;
}
.threatevent ul>li,.threatintelligence ul>li{
  width: 124px;
  height: 22px;
  line-height: 22px;
  background-color: rgba(8,24,67,0.7);
  border-radius: 4px;
  margin-left: 10px;
  float: left;
  margin-bottom: 10px;
  font-size: 12px;
  font-family: FZLTHJW;
  color: #2b6796;
  position: relative;
  cursor: pointer;
}
.threatevent ul>li.addcolor{
  color: #64caff;
}
.threatintelligence ul>li.addcolor{
  color: #64caff;
}
.threatevent ul>li .colorbox,.threatintelligence ul>li .colorbox{
  display: inline-block;
  width: 6px;
  height: 6px;
  background-color: red;
  position: relative;
  top:-1px;
  left:6px;
  margin-right:8px;
}
.threatevent ul>li .colorbox{
  width: 7px;
  height: 7px;
  border-radius: 50%;
}
.threatevent ul>li:nth-child(-n+3) .colorbox{
  border-radius: 0;
  width: 0;
  height: 0;
  border-left: 4.58px solid transparent;
  border-right: 4.58px solid transparent;
  border-bottom: 8px solid red;
}
.threatevent ul>li:nth-child(n+4) .colorbox{
  left: 6.5px;
}
.threatevent ul>li span:nth-child(2),.threatintelligence ul>li span:nth-child(2){
  display: inline-block;
  border: 1px solid #104b79;
  border-radius: 2px;
  width: 12px;
  height: 12px;
  position: absolute;
  top:3.5px;
  right:5px;
}
.threatevent ul>li span.checkbox{
  border-color: #64caff;
  background: url('../../assets/images/check.png') no-repeat 2px 2px
}
.threatintelligence ul>li span.checkbox{
  border-color: #64caff;
  background: url('../../assets/images/check.png') no-repeat 2px 2px
}
</style>



