<template>
  <div class="sz-module list">
    <div class="sz-module-title">
       <el-tabs v-model="activeName" >
        <el-tab-pane label="事件列表" name="first">事件列表</el-tab-pane>
        <el-tab-pane label="攻击者列表" name="second">攻击者列表</el-tab-pane>
        <el-tab-pane label="受攻击资产列表" name="third">受攻击资产列表</el-tab-pane>
      </el-tabs>
    </div>
    <div class = "sz-module-center">
      <chartsList :width="width" :height="height" :options="option1" :class="classname1" :data="data1" v-show="activeName=='first'"></chartsList>
      <chartsList :width="width" :height="height" :options="option2" :class="classname2" :data="data2" v-show="activeName=='second'"></chartsList>
      <chartsList :width="width" :height="height" :options="option3" :class="classname3" :data="data3" v-show="activeName=='third'"></chartsList>
    </div>
  </div>
</template>

<script>
import { index } from '@/api'
import chartsList from '@/components/charts/svt-rollinglist/svt-rollinglist'
export default {
  name: 'list',
  components: {
    chartsList
  },
  props: {},
  data () {
    return {
      // 组件显示高度
      height: 218,
      // 组件显示宽度
      width: 390,
      // 组件应用数据
      data1: [],
      data2: [],
      data3: [],
      activeName: 'first',
      classname1: 'listEvent',
      classname2: 'listEvent2',
      classname3: 'listEvent3',
      option1: {
        // 题头配置
        'header': {
          'colTitName': [
            {
              'name': '首次发生时间',
              'width': 174
            },
            {
              'name': '最近发生时间',
              'width': 170
            },
            {
              'name': '威胁行为',
              'width': 234
            },
            {
              'name': '攻击者IP',
              'width': 107
            },
            {
              'name': '受攻击资产IP',
              'width': 107
            },
            {
              'name': '资产状态',
              'width': 73
            },
            {
              'name': '攻击阶段',
              'width': 82
            },
            {
              'name': '威胁等级',
              'width': 69.5
            },
            {
              'name': '查看详情',
              'width': 75.5
            }
          ],
          // 高度 num
          'hig': 30,
          // 文字颜色his.state  = true
          'fontCor': '#fff',
          // 文字粗体设置
          'fontWeight': 'normal',
          // 背景
          'background': '#3A8ED3',
          'fontFamily': 'FZLTHJW'
        },
        // 行高
        'lineHig': 35,
        // 数据变动数量
        'group': 2,
        // 列表字体缩进
        'ident': 12,
        // 列表字体对齐方式
        'align': 'left',
        // 每列间距
        'colMargin': 2,
        // 列表元素字体大小
        'fontSize': 11,
        'fontFamily': '微软雅黑',
        // 列表元素字体颜色
        'fontCor': '#d4edff',
        // 列表行间隔线类型 solid实线   dotted虚线
        'intervalLine': 'solid',
        // 列表行间隔线颜色
        'intervalCor': 'rgba(231, 231, 231, 0.1)',
        // 行颜色
        'rowBackground': [
          'transparent',
          'transparent'
        ],
        // 特殊元素颜色设置
        'special': [
          [
            '服务器',
            '#CC963C'
          ]
        ],
        // 动画设置
        'animation': {
          // 滚动速度(缓慢模式下) num
          'speed': 2
        },
        'pause': 'mouseover',
        // mouseover or click列表行背景颜色变化
        'rowsCorMouse': 'rgba(0, 0, 0, 0.1)',
        'ele': {}
      },
      option2: {
        // 题头配置
        'header': {
          'colTitName': [
            {
              'name': '攻击者IP',
              'width': 223
            },
            {
              'name': '所属组织',
              'width': 179
            },
            {
              'name': '国家',
              'width': 161
            },
            {
              'name': '攻击者家族',
              'width': 266
            },
            {
              'name': '域名',
              'width': 271
            }
          ],
          // 高度 num
          'hig': 30,
          // 文字颜色his.state  = true
          'fontCor': '#fff',
          // 文字粗体设置
          'fontWeight': 'normal',
          // 背景
          'background': '#3A8ED3'
        },
        // 行高
        'lineHig': 35,
        // 数据变动数量
        'group': 2,
        // 列表字体缩进
        'ident': 12,
        // 列表字体对齐方式
        'align': 'left',
        // 每列间距
        'colMargin': 2,
        // 列表元素字体大小
        'fontSize': 11,
        // 列表元素字体颜色
        'fontCor': '#d4edff',
        // 列表行间隔线类型 solid实线   dotted虚线
        'intervalLine': 'solid',
        // 列表行间隔线颜色
        'intervalCor': 'rgba(255, 255, 255, 0.2)',
        // 行颜色
        'rowBackground': [
          'transparent',
          'transparent'
        ],
        // 特殊元素颜色设置
        'special': [
          [
            '服务器',
            '#CC963C'
          ]
        ],
        // 动画设置
        'animation': {
          // 滚动速度(缓慢模式下) num
          'speed': 2
        },
        // mouseover or click列表行背景颜色变化
        'rowsCorMouse': 'rgba(0, 0, 0, 0.1)',
        'ele': {}
      },
      option3: {
        // 题头配置
        'header': {
          'colTitName': [
            {
              'name': '受攻击资产IP',
              'width': 291
            },
            {
              'name': 'MAC地址',
              'width': 321
            },
            {
              'name': '受攻击次数',
              'width': 229
            },
            {
              'name': '资产状态',
              'width': 261
            }
          ],
          // 高度 num
          'hig': 30,
          // 文字颜色his.state  = true
          'fontCor': '#fff',
          // 文字粗体设置
          'fontWeight': 'normal',
          // 背景
          'background': '#3A8ED3'
        },
        // 行高
        'lineHig': 35,
        // 数据变动数量
        'group': 2,
        // 列表字体缩进
        'ident': 12,
        // 列表字体对齐方式
        'align': 'left',
        // 每列间距
        'colMargin': 2,
        // 列表元素字体大小
        'fontSize': 11,
        // 列表元素字体颜色
        'fontCor': '#d4edff',
        // 列表行间隔线类型 solid实线   dotted虚线
        'intervalLine': 'solid',
        // 列表行间隔线颜色
        'intervalCor': 'rgba(255, 255, 255, 0.2)',
        // 行颜色
        'rowBackground': [
          'transparent',
          'transparent'
        ],
        // 特殊元素颜色设置
        'special': [
          [
            '服务器',
            '#CC963C'
          ]
        ],
        // 动画设置
        'animation': {
          // 滚动速度(缓慢模式下) num
          'speed': 2
        },
        // mouseover or click列表行背景颜色变化
        'rowsCorMouse': 'rgba(0, 0, 0, 0.1)',
        'ele': {}
      }
    }
  },
  // 计算变量
  computed: {
     // 全局时间轴选中的时间
    time () {
      return this.$store.state.time
    }
  },
  // 方法
  methods: {
    getEvent (params) {
      let promise
      promise = index.getEventList(params)
      promise.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          data.forEach((d, i) => {
            let obj = {}
            obj.a = d.startTime
            obj.b = d.latelyTime
            obj.c = d.threatBehavior
            obj.d = `<span style="color:#f9c502">${d.aggressorIP}</span>`
            obj.e = `<span style="color:#f9c502">${d.undeFireAssetIP}</span>`
            obj.f = d.assetState
            obj.g = d.attackStep
            if (d.threatLevel === '高') {
              obj.h = `<span style="color:#ff7200">${d.threatLevel}</span>`
            } else if (d.threatLevel === '中') {
              obj.h = `<span style="color:#f9c502">${d.threatLevel}</span>`
            } else {
              obj.h = `<span style="color:#43c6ff">${d.threatLevel}</span>`
            }
            obj.i = `<span style="cursor:pointer" onclick="window.location=''">查看</span>`
            this.$set(this.data1, i, obj)
          })
        }
      }).catch(err => console.error(err))
    },
    getAggressor (params) {
      let promise2
      promise2 = index.getAggressorList(params)
      promise2.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          data.forEach((d, i) => {
            let obj = {}
            obj.a = `<span style="color:#f9c502">${d.aggressorIP}</span>`
            obj.b = d.affiliateOrganize
            obj.c = d.country
            obj.d = d.aggressorfamily
            obj.e = d.domainName
            this.$set(this.data2, i, obj)
          })
        }
      }).catch(err => console.error(err))
    },
    getUnderFireAsset (params) {
      let promise3
      promise3 = index.getUnderFireAssetList(params)
      promise3.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          data.forEach((d, i) => {
            let obj = {}
            obj.a = `<span style="color:#f9c502">${d.underFireIP}</span>`
            obj.b = d.macAddress
            obj.c = d.underFireCount
            if (d.assetState === '失陷') {
              obj.h = `<span style="color:#d12cd3">${d.assetState}</span>`
            } else if (d.assetState === '疑似失陷') {
              obj.h = `<span style="color:#4659ed">${d.assetState}</span>`
            } else if (d.assetState === '未失陷') {
              obj.h = `<span style="color:#26a3fa">${d.assetState}</span>`
            } else {
              obj.h = `<span style="color:#6be9f5">${d.assetState}</span>`
            }
            this.$set(this.data3, i, obj)
          })
        }
      }).catch(err => console.error(err))
    }
  },
  // 监听
  watch: {
    time (val, oldval) {
      let params = {time: this.time}
      this.getEvent(params)
      this.getAggressor(params)
      this.getUnderFireAsset(params)
    }
  },
  // 更新之后执行
  updated () {
    this.isLoad = false
  },
  mounted () {
    let params = {time: this.$store.state.time}
    this.getEvent(params)
    this.getAggressor(params)
    this.getUnderFireAsset(params)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.list{
  width: 1110px;
  height: 258px;
  top: 802px;
  left: 15px;
  padding: 4px 4px;
}
.el-tabs__nav-wrap::after{
  height: 0px;
}
.el-tabs__item{
  color: #5e95bc ;
  height: 20px;
  line-height: 20px;
  border-right: 1px solid #5e95bc;
  font-family:FZLTHJW;
}
.el-tabs__item:hover{
  color: #d4edff;
}
.el-tabs__item.is-active{
  color: #d4edff;
}
.el-tabs__item:last-child{
  border-right: none;
}
.el-tabs__active-bar{
  display: none;
}
.el-tabs__content{
  display: none;
}
.listEvent .svt-charts-rolling .box-list .svt-list:nth-child(4){
  color: #f9c502
}
.list .sz-module-center{
  margin-top: 6px;
  height: 212px;
  overflow: hidden;
}
</style>



