/*
 *     Created on  2018-07-05 13:41:31
 *     @author: LJG
 *     @summary: svtcharts-柱图组件
 *     @version: v1.0.0
 *     Last Modified: 2018-07-05 13:41:32
 *     Modified By: liujianguo
 */
<template>
  <div @mouseout="onOut">
    <svg :width="width" :height="height" ref="svgRoot">
      <g class='svt-charts-column'>
        <svt-axis orient="axisBottom" :scale="scaleX" :options="axisOptionsX" v-if="isXaxis" v-on:dTick="drawXTick"/>
        <svt-axis orient="axisLeft" :scale="scaleY" :options="axisOptionsY" v-on:dTick="drawYTick"/>
        <g :transform="translate" :clip-path="random2">
          <!-- 悬浮矩形背景 -->
          <!-- <rect class="backgroud-rect" :width="backWid" :height="heightColum" x="0" y="0" fill="rgba(100,100,100,0.6)"></rect> -->
          <!-- 正常柱图 -->
          <g v-for="(arr, index) in data" :key="index" :transform="groups(index, arr)" class="svt-cloumn-groups">
            <rect v-for="(val, idx) in data[index].a" :key="idx"
                  v-if="options.type === 'normal'"
                  :width="columnWid"
                  :height="yHig(data[index].a[idx].num, idx)"
                  :x="-columnWid * 0.5"
                  :y="yStarNormal(data[index].a, data[index].a[idx].num, idx)"
                  :fill="options.column.color[index][idx]"
                  class="cloumn"
            >
            </rect>
            <!-- 堆叠柱图 -->
            <g v-for="(val, idx) in data[index].a" :key="idx"
                v-if="options.type === 'stack'"
                :transform="yStarStack(data[index].a, data[index].a[idx].num, idx)"
                >
              <rect v-for="ind in stackRange(data[index].a, data[index].a[idx].num, idx)" :key="ind"
                  :width="columnWid"
                  :height="options.column.stackHig"
                  :x="-columnWid * 0.5"
                  :y="yStarSmall(ind)"
                  :fill="options.column.color[index][idx]"
                  class="cloumn">
              </rect>
            </g>
            <!-- 多列柱图 -->
            <rect v-for="(val, idx) in data[index].a" :key="idx"
                  v-if="options.type === 'moreCols'"
                  :width="moreWid"
                  :height="yHig(data[index].a[idx].num, idx)"
                  :x="xStart(idx)"
                  :y="yStarNormal(data[index].a, data[index].a[idx].num, idx)"
                  :fill="options.column.color[index][idx]"
                  class="cloumn"
            >
            </rect>
            <!-- 补背景柱图 -->
            <rect :width="columnWid"
                  :height="supplementHeight(index)"
                  :x="-columnWid * 0.5"
                  :y="0"
                  :fill="options.column.supplement.color"
                  class="supplement-cloumn"
                  v-show="options.column.supplement.show"
            >
            </rect>
          </g>
          <defs><!-- 定义 -->
            <clipPath :id="randomId"><!-- 定义剪裁路径 -->
                <rect x="0" y="0" :width="width" :height="height - options.marginBottom - options.marginTop" /><!-- 路径详细 -->
            </clipPath>
          </defs>
        </g>
      </g>
    </svg>
    <!-- 提示框dom -->
    <svt-tooltip :data="tooltipData" :x="tooltipX" :y="tooltipY"/>
    <svt-column-plane class="svt-column-pane" @move="onMove" @drag="onDrag" @scroll="onScroll" :style="paneOptions"> </svt-column-plane>
  </div>
</template>

<script>
import svtAxis from '../svt-common/svt-axis'
import svtColumnPlane from './svt-column-plane'
import svtTooltip from './../svt-common/svt-tooltip'
import * as d3 from 'd3'
import $ from 'jquery'
export default {
  name: 'svt-column',
  data () {
    return {
      animation: [],
      ishig: [],
      isXaxis: false,
      isRun: false,
      isMout: 0,
      tooltipIdx: 0,
      interval: [0, 10],
      // // 数值范围X
      // domainX: [0,10],
      // 数值范围Y
      domainY: [100, 1],
      // 提示框位置X
      tooltipX: 0,
      // 提示框位置Y
      tooltipY: 0,
      // 提示框显示内容（html格式）
      tooltipData: '',
      // 随机Id
      randomId: Number(new Date()) + 'clipPath'
    }
  },
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    options: {
      type: Object,
      default: function () {
        return {
          // 柱图类型
          type: 'normal',
          // 图表区域位置
          transform: 'translate(0, 0)',
          // 柱图样式配置
          column: {
            // 单列宽度
            wid: 37,
            // 颜色
            color: ['#9172EA', '#3A8ED3']
          }
        }
      }
    },
    data: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  watch: {
    'data': {
      handler () {
        if (this.data !== 0) {
          this.isXaxis = true
          this.run()
        }
      },
      deep: true
    },
    width () {
      this.run()
    },
    height () {
      this.run()
    },
    'options': {
      handler () {
        this.run()
      },
      deep: true
    },
    domainX () {
      this.changeX()
    }
  },
  computed: {
    // 生成随机裁剪id
    random2 () {
      return `url(#${this.randomId})`
    },
    // 深拷贝后的配置项
    _options () {
      let options = $.extend(true, {}, this.options)
      options.axis.yStyle.font.size = parseInt(options.axis.yStyle.font.size)
      return options
    },
    tooptipArray () {
      if (this.isXaxis) {
        return this.data[this.tooltipIdx].a
      }
    },
    domainX () {
      return this.data.length > 0 ?[0, this.data.length + 1 ] : [0, 1]
    },
    // 计算当前比例尺应用范围
    range () {
      const { marginTop, marginRight, marginBottom, marginLeft } = this.options
      return {
        x: [0, this.width - marginRight - marginLeft],
        y: [0, this.height - marginTop - marginBottom]
      }
    },
    // 设置x轴比例尺
    scaleX () {
      const {range, domainX} = this
      return d3.scaleLinear().range(range.x).domain(domainX)
    },
    // 设置y轴比例尺
    scaleY () {
      const { data, options } = this
      let column = []
      for (let i = 0; i < data.length; i++) {
        let val = 0
        if (options.type === 'normal' || options.type === 'stack') {
          for (let j = 0; j < data[i].a.length; j++) {
            val += data[i].a[j].num
          }
          column.push(val)
        } else if (options.type === 'moreCols') {
          for (let j = 0; j < data[i].a.length; j++) {
            val = data[i].a[j].num
            column.push(val)
          }
        }
      }
      const domainY = [Math.max(...column), 0]
      const {range} = this
      return d3.scaleLinear().range(range.y).domain(domainY)
    },
    // 传入配置参数X
    axisOptionsX () {
      const bottom = this.options.axise
      return {
        transform: `translate(${this.options.marginLeft}px, ${this.height - this.options.marginBottom}px)`,
        color: bottom.color,
        fontSize: bottom.text.fontSize,
        fontFamily: bottom.text.fontSize,
        tickFormat: this.axisFormat
      }
    },
    // 传入配置参数Y
    axisOptionsY () {
      const bottom = this.options.axise
      return {
        transform: `translate(${this.options.marginLeft}px, ${this.options.marginTop}px)`,
        color: bottom.color,
        fontSize: bottom.text.fontSize,
        fontFamily: bottom.text.fontSize,
        tickFormat: '',
        tick: bottom.yAxis.ticks
      }
    },
    widthColum () {
      return this.width - this.options.marginRight - this.options.marginLeft
    },
    heightColum () {
      return this.height - this.options.marginTop - this.options.marginBottom
    },
    columnWid () {
      return this.options.column.wid
    },
    moreWid () {
      const { columnWid, options, data } = this
      let margins = options.column.colMargin * (data[0].a.length - 1)
      return (columnWid - margins) / data[0].a.length
    },
    translate () {
      let translate = `translate(${this.options.marginLeft}, ${this.options.marginTop})`
      return translate
    },
    xTranslate () {
      let translate = `translate(${this.options.marginLeft}, ${this.height - this.options.marginBottom})`
      return translate
    },
    yTranslate () {
      let translate = `translate(${this.options.marginLeft}, ${this.options.marginTop})`
      return translate
    },
    // 设置事件容器样式
    paneOptions () {
      return {
        position: 'absolute',
        transform: `translate(${this.options.marginLeft}px, ${this.options.marginTop}px)`,
        width: this.widthColum + 'px',
        height: this.heightColum + 'px',
        top: 0,
        left: 0
      }
    },
    // 矩形背景宽度
    backWid () {
      return 2 * this.options.column.wid
    }
  },
  methods: {
    // 补充背景高度
    supplementHeight (index) {
      let val = 0
      this.data[index].a.forEach(function (item, index) {
        val += item.num
      })
      return this.scaleY(val)
    },
    // 重绘y轴的先关配置项
    drawYTick (ele) {
      // 是否显示坐标轴上的线
      if (!this.options.axise.yAxis.lineShow) {
        ele.selectAll('path').style('display', 'none')
      }
      // 是否显示坐标轴上tick
      if (!this.options.axise.yAxis.tickShow) {
        ele.selectAll('line').style('display', 'none')
      }
      // 生成刻度长线
      let tickLine
      if (this.options.axise.yAxis.tickLine.show) {
        tickLine = ele
          .selectAll('.tick')
          .append('line')
          .attr('x1', 0)
          .attr('y1', 0)
          .attr('x2', this.widthColum)
          .attr('y2', 0)
      }
      if (this.options.axise.yAxis.tickLine.show && this.options.axise.yAxis.tickLine.linear === 'dotted') {
        tickLine.attr('stroke-dasharray', '5,5')
      }
      // 重置坐标轴文字颜色
      ele.selectAll('text').style('fill', this.options.axise.text.color ? this.options.axise.text.color : '#fff')
    },
    // 重新绘制x轴相关配置项
    drawXTick (ele) {
      // 是否显示坐标轴上的线
      if (!this.options.axise.xAxis.lineShow) {
        ele.selectAll('path').style('display', 'none')
      }
      // 是否显示坐标轴上tick
      if (!this.options.axise.xAxis.tickShow) {
        ele.selectAll('line').style('display', 'none')
      }
      // 生成刻度长线
      let tickLine
      if (this.options.axise.xAxis.tickLine.show) {
        tickLine = ele
          .selectAll('.tick')
          .append('line')
          .attr('x1', 0)
          .attr('y1', 0)
          .attr('x2', 0)
          .attr('y2', 0 - this.heightColum)
      }
      if (this.options.axise.xAxis.tickLine.show && this.options.axise.xAxis.tickLine.linear === 'dotted') {
        tickLine.attr('stroke-dasharray', '5,5')
      }
      // 重置坐标轴文字颜色
      ele.selectAll('text').style('fill', this.options.axise.text.color ? this.options.axise.text.color : '#fff')
    },
    // 横坐标值转换
    axisFormat (i) {
      if (i > 0 && i <= this.data.length && i === Math.round(i)) {
        return this.options.axise.xAxis.formate.show ? this.options.axise.xAxis.formate.center[i-1] : this.data[i - 1].base
      }
    },
    groups (index, arr) {
      return `translate(${this.scaleX(index + 1)}, ${this.heightColum})`
    },
    xStart (idx) {
      const { columnWid, moreWid, options } = this
      let start = -columnWid * 0.5
      return start + idx * (moreWid + options.column.colMargin)
    },
    // 正常矩形起始位置
    yStarNormal (d, val, i) {
      const { heightColum, scaleY, options } = this
      if (options.type === 'moreCols') {
        return scaleY(val)
      }
      // 单列多组柱图返回起始位置
      let start = 0
      for (let j = 0; j < i; j++) {
        // 同列上一个矩形的高度
        start += heightColum - scaleY(d[j].num)
      }
      return scaleY(val) - start
    },
    // 堆叠矩形组起始位置
    yStarStack (d, val, i) {
      const { heightColum, scaleY, options } = this
      // 起始点
      let start = 0
      let rectY = options.column.stackHig + options.column.margin
      let result = (heightColum - scaleY(val)) / rectY
      let startY = heightColum - (Math.round(result) + 1) * (rectY)
      for (let j = 0; j < i; j++) {
        let results = (heightColum - scaleY(d[j].num)) / rectY
        start += Math.round(results) * (rectY)
      }
      return `translate(0, ${startY - start})`
    },
    yHig (val, i) {
      return this.heightColum - this.scaleY(val)
    },
    // 小矩形块个数（对应正常柱图的单个颜色片段）
    stackRange (d, val, i) {
      const { heightColum, scaleY, options } = this
      let rectY = options.column.stackHig + options.column.margin
      let result = (heightColum - scaleY(val)) / rectY
      return Math.round(result)
    },
    // 堆叠小矩形起始位置
    yStarSmall (i) {
      const { options } = this
      let rectY = options.column.stackHig + options.column.margin
      return rectY * i
    },
    // 开始动画
    run () {
      this.$nextTick(function () {
        let _this = this
        d3.selectAll('g.svt-cloumn-groups')
          .attr('transform', function () {
            let x = d3.select(this).attr('transform')
              .replace('translate(', '').replace(')', '').split(',')[0]
            return `translate(${x}, ${_this.heightColum})`
          })
          .transition(1000).duration(1000)
          .attr('transform', function () {
            let x = d3.select(this).attr('transform')
              .replace('translate(', '').replace(')', '').split(',')[0]
            return `translate(${x}, 0)`
          })
      })
    },
    // 缩放重置位置
    changeX () {
      this.$nextTick(function () {
        d3.selectAll('g.svt-cloumn-groups')
          .attr('transform', function () {
            let x = d3.select(this).attr('transform')
              .replace('translate(', '').replace(')', '').split(',')[0]
            return `translate(${x}, 0)`
          })
      })
    },
    // 绑定移动事件，获取鼠标位置
    onMove (x, y) {
      this.highlight(x, y)
    },
    // 绑定缩放事件，获取偏移量&倍数
    onScroll (x, delta) {
      const factor = delta > 0 ? 1.1 : 0.9
      const {zoom, onOut} = this
      zoom(x, factor)
      onOut()
    },
    // 通过偏移量&倍数缩放视图
    zoom (x, factor) {
      const {scaleX, widthColum} = this
      const defaultDomain = this.interval
      const domain = [scaleX.invert(x - x * factor), scaleX.invert(x + (widthColum - x) * factor)]
      this.domainX = [Math.max(defaultDomain[0], domain[0]), Math.min(defaultDomain[1], domain[1])]
    },
    // 拖拽功能
    onDrag (delta) {
      const {scaleX, range, onOut} = this
      const defaultDomain = this.interval
      let x1 = scaleX.invert(range.x[0] - delta)
      let x2 = scaleX.invert(range.x[1] - delta)
      if (defaultDomain[0] < x1 && x2 < defaultDomain[1]) this.domainX = [x1, x2]
      onOut()
    },
    // 悬浮显示数据
    highlight (x, y) {
      const {scaleX, options} = this
      const time = [scaleX.invert(x - options.column.wid / 2), scaleX.invert(x), scaleX.invert(x + options.column.wid / 2)]
      this.data.forEach((d, i) => {
        // 判断鼠标的位置在柱的区域内
        if (time[0] <= (i + 1) && time[2] >= (i + 1)) {
          this.isMout = i + 1
          this.tooltipIdx = i
          d3.select('.backgroud-rect')
            .attr('x', this.scaleX(i + 1) - options.column.wid)
            .style('display', 'block')
          // 提示框组件添加数据
          this.tooltipData = ''
          // this.tooptipArray.forEach((d, i) => {
            this.tooltipData += `
              <div>
                <span style="font-size: 0">
                  <span style="
                      display: inline-block;
                      width: 12px;
                      height: 12px;
                      border-radius: 12px;
                      vertical-align: middle;
                      background-color: ${options.tipcolor[i]};"></span>
                </span>
                <span>${d.a[0].type + '：' + d.a[0].num + ' ' + this.options.tooltip.unit}</span>
              </div>
            `
          // })
          this.tooltipX = x + options.column.wid * 2
          this.tooltipY = y + options.column.wid
        } else if (time[0] > this.isMout || time[2] < this.isMout) {
          d3.select('.backgroud-rect').style('display', 'none')
          this.tooltipData = ''
        }
      })
    },
    // 隐藏辅助图表
    onOut () {
      d3.select('.backgroud-rect').style('display', 'none')
      this.tooltipData = ''
    }
  },
  mounted () {
  },
  components: {
    svtAxis,
    svtColumnPlane,
    svtTooltip
  }
}
</script>
<style scoped>
.backgroud-rect {
  display: none
}
.svt-column-rows {
  height: 25px;
  line-height: 25px;
  width: 170px;
  border: 1px solid #fff;
}
.svt-column-rows span {
  display: inline-block;
  text-align: left;
}
#svt-column-circle {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin-left: 5px;
  position: relative;
  top: 19px;
}
.svt-column-name {
  width: 70px;
  margin-left: 5px
}
.svt-column-value {
  width: 50px;
}
</style>



