 */
<template>
  <g :style="{transform: options.transform}"></g>
</template>

<script>
import * as d3 from 'd3'
// 继承父元素属性
const props = {
  // 坐标轴方向（axisTop | axisBottom | axisRight | axisLeft
  orient: {
    type: String,
    default: 'axisTop'
  },
  // 坐标轴应用比例尺
  scale: {
    type: Function,
    required: true
  },
  // 坐标轴配置项
  options: {
    type: Object,
    default: () => {
      return {}
    }
  }
}
// 返回组件
export default {
  name: 'svt-axis',
  props,
  mounted () {
    this._draw()
  },
  methods: {
    // 绘制坐标轴
    _draw () {
      let _scale = d3[this.orient](this.scale)
      if (this.options.tickPadding) _scale.tickPadding(this.options.tickPadding)
      if (this.options.tickFormat) _scale.tickFormat(this.options.tickFormat)
      if (this.options.tick)  _scale.ticks(this.options.tick)

      d3.select(this.$el).call(_scale)
      // qlq增加，添加刻度长线
      this.$emit('dTick', d3.select(this.$el))
      this.changeStyles()
    },
    // 更改坐标轴样式
    changeStyles () {
      let ele = d3.select(this.$el)
      ele.attr('font-size', this.options.fontSize).attr('font-family', this.options.fontFamily)
      ele.selectAll('path, line').attr('stroke', this.options.color)
      ele.selectAll('text').attr('fill', this.options.color)
    }
  },
  watch: {
    // 监听比例尺变化
    scale () {
      this._draw()
    },
    // 监听配置项变化
    options () {
      this.changeStyles()
    }
  }
}
</script>



