<template>
    <div id='svt-charts-rolling' class='container' :class="classname">
        <!-- 题头 -->
        <div class="cells" :style="styl(key, 'title')" v-for="(value, key) in headData" :key="key">
            {{ value.name }}
        </div>
        <!-- 列表内容 -->
        <div class="svt-charts-rolling" :style='higs'>
            <ul class="box-list">
                <li class="svt-list"
                  v-for="(arr, index) in dataFirst" :key="index"
                  :style="styleLi(index)"
                  @mousemove="options.pause === 'mouseover' ? stop(index) : ''"
                  @mouseout="options.pause === 'mouseover' ? running(index): ''"
                  @click="options.pause === 'click' ? listClick(index): ''">
                    <div class="cells2"
                    :style="styl(idx, 'list', value)"
                    v-for="(value, key, idx) in arr" :key="idx"
                    @mousemove="tootip(value, $event)"
                    @mouseout="tootipOut"
                        v-html="value">
                    </div>
                </li>
            </ul>
        </div>
      <!-- 提示框dom -->
      <svt-tooltip :data="tooltipData" :x="tooltipX" :y="tooltipY"/>
    </div>
</template>

<style scoped>
  @import url(./clear-style.sass);
</style>

<script>
import svtTooltip from './../svt-common/svt-tooltip'
import $ from 'jquery'
export default {
  name: 'charts-list',
  data () {
    return {
      // 表头数据
      headData: [],
      // 动画实例
      animation: false,
      // 帧
      countStep: 0,
      // 第一行
      start: 0,
      // 最后一行
      end: 0,
      // 动画执行条件
      isRun: '',
      // 记录行颜色
      rowMouseBg: '',
      // 记录上一次点击索引
      selectIdx: null,
      // 界面上看到的数据
      dataFirst: [],
      // 更新数组
      dataEles: [],
      // 点击判断变量
      clickNum: 0,
      // 悬浮框数据
      tooltipData: '',
      // 悬浮框横坐标
      tooltipX: -100,
      // 悬浮框纵坐标
      tooltipY: -100
    }
  },
  // props: ['options'],
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    classname: {
      type: String,
      default: ''
    },
    options: {
      type: Object,
      default: function () {
        return {
          // 柱图类型
          type: 'normal',
          // 图表区域位置
          transform: 'translate(0, 0)',
          // 比例尺X
          scaleX: '',
          // 比例尺Y
          scaleY: '',
          // 柱图样式配置
          column: {
            // 单列宽度
            wid: 37,
            // 颜色
            color: ['#9172EA', '#3A8ED3']
          }
        }
      }
    },
    data: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  computed: {
    higs: function () {
      // - 5 为了抵消ul自带边距
      let firstHig = this.height - this.options.header.hig - 5
      return {
        height: `${firstHig}px`
      }
    },
    // 列表数据
    max: function () {
      let firstHig = this.height - this.options.header.hig - 5
      // 可见数据条数
      let max = Math.floor(firstHig / (this.options.lineHig + 1))
      return max
    }
  },
  watch: {
    'data': {
      handler () {
        if (this.data !== 0) {
          cancelAnimationFrame(this.animation)
          this.draw()
        }
      },
      deep: true
    },
    'options.lineHig': {
      handler () {
        cancelAnimationFrame(this.animation)
        this.draw()
      },
      deep: true
    },
    height () {
      cancelAnimationFrame(this.animation)
      this.draw()
    }
  },
  methods: {
    styl (idx, type, val) {
      // 列表与题头公共样式
      let styleBase = {
        width: `${this.options.header.colTitName[idx].width}px`,
        'font-size': `${this.options.fontSize}px`,
        'text-indent': `${this.options.ident}px`,
        'text-align': this.options.align,
        'font-family': `${this.options.fontFamily}`
      }
      // 题头样式
      let stylesTit = {
        color: this.options.header.fontCor,
        height: `${this.options.header.hig}px`,
        background: this.options.header.background,
        'font-weight': this.options.header.fontWeight,
        'line-height': `${this.options.header.hig}px`,
        'margin-left': `${this.options.colMargin}px`,
        'font-family': `${this.options.header.fontFamily}`
      }
      // 列表样式
      let stylesList = {
        color: this.colors(val),
        height: `${this.options.lineHig}px`,
        'line-height': `${this.options.lineHig}px`
      }
      switch (type) {
        case 'list':
          return Object.assign({}, styleBase, stylesList)
        case 'title':
          return Object.assign({}, styleBase, stylesTit)
        default:
          return styleBase
      }
    },
    styleLi: function (index) {
      return {
        height: `${this.options.lineHig}px`,
        'border-bottom': `1px ${this.options.intervalLine} ${this.options.intervalCor}`,
        'background-color': this.options.rowBackground[index % 2]
      }
    },
    tootip (val) {
      this.tooltipData = `
        <span style="padding: 5px 5px;font-size:12px">${val.toString()}</span>
      `
      this.tooltipX = event.clientX - 70
      this.tooltipY = event.clientY - 800
      // console.log(this.tooltipX,this.tooltipY)
    },
    tootipOut () {
      this.tooltipData = ''
    },
    draw () {
      // 初始化条件 start
      this.dataFirst = []
      this.dataEles = []
      const className = this.$el.getAttribute('class').split(' ')[1]
      document.querySelector(`.${className} .box-list`).style.top = 0
      // 初始化条件 end
      this.headData = this.options.header.colTitName
      this.data.forEach((d, i) => {
        if (i < this.max * 2) {
          this.dataFirst.push(this.data[i])
        } else {
          this.dataEles.push(this.data[i])
        }
      })
      // 防止在内容可见区域出现数据替换 (当数据长度位于此临界条件时)
      if (this.data.length > this.max && this.data.length < this.max + this.options.group) {
        this.dataFirst = [...this.dataFirst, ...this.dataFirst]
      }
      this.isRun = this.$route.path
      this.run()
    },
    run () {
      if ((this.data.length > this.max) && this.isRun === this.$route.path) {
        // 动画执行
        this.animation = requestAnimationFrame(this.run)
        const className = this.$el.getAttribute('class').split(' ')[1]
        if (this.countStep % this.options.animation.speed === 0) {
          const dom = document.querySelector(`.${className} .box-list`)
          // const list1 = document.querySelector('.list1 .box-list')
          // const list2 = document.querySelector('.list2 .box-list')
          let top = Number(getComputedStyle(dom).top.replace('px', '')) - 1
          dom.style.top = `${top}px`
          if (top === -(this.options.lineHig + 1) * this.options.group) {
            for (let i = 0; i < this.options.group; i++) {
              this.dataEles.push(this.dataFirst[0])
              this.dataFirst.shift()
              let num = this.dataEles[0]
              this.dataFirst.push(num)
              this.dataEles.shift()
            }
            dom.style.top = `0px`
            // list2.style.top = `15px`
            // list1.style.top = `0px`
          }
        }
        this.countStep++
      }
    },
    stop (index) {
      this.rowMouseBg = this.options.rowBackground[index % 2]
      const className = this.$el.getAttribute('class').split(' ')[1]
      $(`.${className} .svt-list`).eq(index).css('background-color', this.options.rowsCorMouse)
      // document.getElementsByClassName('svt-list')[index].style.backgroundColor = this.options.rowsCorMouse
      cancelAnimationFrame(this.animation)
    },
    running (index) {
      const className = this.$el.getAttribute('class').split(' ')[1]
      $(`.${className} .svt-list`).eq(index).css('background-color', this.rowMouseBg)
      // document.getElementsByClassName('svt-list')[index].style.backgroundColor = this.rowMouseBg
      this.animation = requestAnimationFrame(this.run)
    },
    listClick (index) {
      // 重置行背景色
      if (this.selectIdx || this.selectIdx === 0) {
        document.getElementsByClassName('svt-list')[this.selectIdx].style.backgroundColor =
          this.options.rowBackground[this.selectIdx % 2]
      }
      // 判断点击行不相等 或者 同一行点击超过一次
      if (this.selectIdx !== index || this.clickNum > 0) {
        this.stop(index)
        this.selectIdx = index
        this.clickNum = 0
      } else {
        // 行点击计数变量
        this.clickNum++
        cancelAnimationFrame(this.animation)
        this.running(index)
      }
    },
    colors (d) {
      for (var i = 0; i <= this.options.special.length; i++) {
        if (i === this.options.special.length) {
          return this.options.fontCor
        }
        if (d === this.options.special[i][0]) {
          return this.options.special[i][1]
        }
      }
    }
  },
  mounted () {

  },
  components: {
    svtTooltip
  }
}
</script>



