<template>
  <div class="sz-module underFireAsset">
    <div class="sz-module-title">
       受攻击资产
    </div>
    <div class = "sz-module-center">
      <div class="piebox">
        <div class="pietitle">资产状态</div>
        <div class="piecontent">
            <svt-pie :data="data" :class="classname" :options="options" :width="width" :height="height"></svt-pie>
        </div>
        <div class="pielegend">
            <svt-legend class="svt-legends" :options="optionLegend" :data="dataLegend"> </svt-legend>
        </div>
        <ul class="border">
          <li v-for="(item,index) in borders" :key="index"></li>
        </ul>
      </div>
      <div class="cloumnbox">
        <div class="cloumntitle">资产受攻击次数排行</div>
        <svt-column :width="cwidth" :height="cheight" :options="coptions" :data="cdatas"/>
      </div>
      <div class="treeMapbox">
        <div class="treeMapboxtitle">受攻击阶段</div>
        <div class="treeMapboxCenter">
           <svtTreeMap :data = "treeMap.data" :width= "treeMap.width" :height= "treeMap.height" :options= "treeMap.options"></svtTreeMap>
        </div>
      </div>
    </div>
    <svg>
      <defs>
          <linearGradient id="reactfill1" x1="100%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:#ffcd0c;stop-opacity:1" />
            <stop offset="100%" style="stop-color:#ff9a00;stop-opacity:1" />
          </linearGradient>
        </defs>
      <defs>
        <linearGradient id="reactfill2" x1="100%" y1="0%" x2="100%" y2="100%">
          <stop offset="0%" style="stop-color:#abf2ff;stop-opacity:1" />
          <stop offset="100%" style="stop-color:#8bceda;stop-opacity:1" />
        </linearGradient>
      </defs>
      <defs>
        <linearGradient id="reactfill3" x1="0%" y1="100%" x2="100%" y2="100%">
          <stop offset="0%" style="stop-color:#ff9a00;stop-opacity:1" />
          <stop offset="100%" style="stop-color:#ffcd0c;stop-opacity:1" />
        </linearGradient>
      </defs>
      <defs>
        <linearGradient id="reactfill4" x1="0%" y1="100%" x2="100%" y2="100%">
          <stop offset="0%" style="stop-color:#8bceda;stop-opacity:1" />
          <stop offset="100%" style="stop-color:#abf2ff;stop-opacity:1" />
        </linearGradient>
      </defs>
    </svg>
  </div>
</template>

<script>
import { index } from '@/api'
import svtPie from '@/components/charts/svt-pie/svt-pie'
import svtLegend from '@/components/charts/svt-common/svt-legend'
import svtColumn from '@/components/charts/svt-column/svt-column'
import svtTreeMap from '@/components/charts/svt-treeMap/svt-treeMap'

export default {
  name: 'underFireAsset',
  components: {
    svtPie,
    svtLegend,
    svtColumn,
    svtTreeMap
  },
  props: {},
  data () {
    return {
      // 容器宽度
      width: 130,
      classname: 'visitedPie',
      // 容器高度
      height: 160,
      // 配置项
      options: {
        // 悬浮提示数值还是百分比 true为数值，false为百分比
        tooltiptype: true,
        // type1正常 type2花瓣图
        type: 'type1',
        // 外圆弧半径
        outerRadius: 65,
        // 内圆弧半径
        innerRadius: 40,
        // 上边距
        magrinTop: 0,
        // 下边距
        magrinBottom: 0,
        // 左边距
        magrinLeft: 0,
        // 右边距
        magrinRight: 0,
        // 文字字号
        fontSize: 20,
        // 速度
        speed: 700,
        // color
        color: ['#d12cd3', '#4659ed', '#26a3fa', '#6be9f5'],
        typeName: ['高危', '中危', '低危', '低危'],
        shadow: {
          show: false,
          outerRadius: 65,
          width: 100,
          innerRadius: 20,
          color: 'rgba(0,0,0,.1)'
        },
        text: {
          show: false,
          porotor: true,
          color: '#fff'
        },
        line: {
          color: '#ccc'
        },
        tooltip: {
          unit: '个'
        }
      },
      data: [],
      values: [],
      names: [],
      borders: [1, 2, 3, 4],
      // 图例配置项    ------------------------------------------------- //图例配置项
      optionLegend: {
        // 图例组行高
        rowHeight: 25,
        // 图形配置
        grap: {
          // 'circle' or 'rectangle'
          how: 'rectangle',
          circle: {
            // 半径
            r: 5
          },
          rectangle: {
            // 宽度
            width: 8,
            // 高度
            height: 3,
            // 内边框 1px solid red
            in: 'none',
            // 外边框
            out: 'none'
          },
          color: ['#d12cd3', '#4659ed', '#26a3fa', '#6be9f5']
        },
        // name配置
        name: {
          // 宽度
          width: 50,
          // 左边距
          left: 5,
          // 默认颜色
          color: ['#d4edff'],
          // 字体大小
          fontSize: 12,
          // 字体样式
          fontFamily: '微软雅黑',
          // 字体对齐方式
          align: 'left'
        },
        // value配置
        value: {
          // 宽度
          width: 0,
          // 左边距
          left: -10,
          // 默认颜色
          color: ['#05acf2', '#009879', '#6546cc', '#6546cc'],
          // 字体大小
          fontSize: 12,
          // 字体样式
          fontFamily: 'PBold',
          // 字体对齐方式
          align: 'right'
        }
      },
      // 展示页标题
      name: '饼图',
      // input行统一样式，必须写
      labelPosition: 'right',
      // 柱图---------------------------
      columnshadow: true,
      // 组件区域宽度
      cwidth: 682,
      // 组件区域高度
      cheight: 151,
      coptions: {
        className: 'column1',
        marginTop: 25,
        marginBottom: 25,
        marginLeft: 30,
        marginRight: 25,
        // 悬浮提示的颜色
        tipcolor: ['#ff9900', '#ff9900', '#ff9900', '#6ccdff', '#6ccdff', '#6ccdff', '#6ccdff'],
        // 柱图类型 normal, stack, moreCols
        type: 'stack',
        // 柱形样式配置
        column: {
          // 单列宽度
          wid: 37,
          // 颜色
          color: [['#ffb005'], ['#ffb005'], ['#ffb005'], ['#9fe4f1'],['#9fe4f1'], ['#9fe4f1'], ['#9fe4f1']],
          // 小矩形块高度
          stackHig: 6,
          // 小矩形块间隔
          margin: 1,
          // 多列矩形间隔
          colMargin: 2,
          // 背景颜色
          bgcolor: 'red',
          //  补充到全高背景设置
          supplement: {
            show: false,
            color: "red"
          }
        },
         axise: {
          // 坐标轴颜色
          color: '#495d78',
          // 坐标轴文字样式
          text: {
            fontFamily: 'FZLTHJW',
            fontSize: 12,
            color: '#d4edff'
          },
          // 横轴配置项
          xAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: true,
            // 刻度长线是否展示
            tickLine: {
              show: false,
              // 线形：dotted虚线，solid实线
              linear: 'dotted'
            },
            // 设置纵轴显示的刻度数
            ticks: 6,
            // 自定义横轴显示的信息
            // 注意：使用了横轴显示信息后，center数组的数量应该与数据的条数一样多
            // 不需要显示的使用空字符串
            formate: {
              show: true,
              center: []
            }
          },
          yAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: false,
            // 刻度长线是否展示
            tickLine: {
              show: true,
              // 线形：dotted虚线，solid实线
              linear: 'dotted'
            },
            // 设置纵轴显示的刻度数
            ticks: 4
          }
        },
        tooltip: {
          unit: '次'
        }
      },
      cdatas: [],
      // 展示页标题
      cname: '柱图',
      // 坐标轴设置开关
      axisShow: false,
      // 柱图样式z设置开关
      columnShow: false,
      // 受攻击阶段
      treeMap:{
        width: 387,
        height: 123,
        options: {
          color:['#ffde00','#4bbdff','#795fff','#ff7200'],
          padding: 3,
          text: {
            'fontSize': 12,
            'fontFamily':'微软雅黑',
            'color': '#383f4c',
            'fontWeight': 800
          },
          tooltip: {
            unit: '个'
          }
        },
        data:{}
      }
    }
  },
  // 计算变量
  computed: {
    // 获得格式化组件数据内容
    pieOptions () {
      return JSON.stringify(this.options, null, 2)
    },
    // 获得格式化组件配置内容
    pieData () {
      return JSON.stringify(this.data, null, 2)
    },
    // 图例展示数据
    dataLegend () {
      let legendArr = []
      for (let i = 0; i < this.names.length; i++) {
        legendArr.push({name: this.names[i]})
        // this.computedwarnnum()
      }
      return legendArr
    },
    // 全局时间轴选中的时间
    time () {
      return this.$store.state.time
    }
  },
  // 方法
  methods: {
    getState (params) {
      // 资产状态数据
      let promise = index.getUnderFireAssetState(params)
      promise.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          this.data = data
          for (let i = 0; i < this.data.length; i++) {
            this.$set(this.names, i, this.data[i].name)
            this.$set(this.values, i, this.data[i].value)
          }
        } else {
          let valueArr = ['失陷', '疑似失陷', '未失陷', '未知']
          for (let i = 0; i < 4; i++) {
            this.$set(this.names, i, valueArr[i])
            this.$set(this.values, i, 0)
          }
        }
      }).catch(err => console.error(err))
    },
    getTime (params) {
      // 资产受攻击次数排行数据
      let promise2 = index.getUnderFireAssetTime(params)
      promise2.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          let transdata = []
          for (let i = 0; i < data.length; i++) {
            data[i].name.length > 14 ? this.coptions.axise.xAxis.formate.center.push(data[i].name.substring(0,12)+'..') : this.coptions.axise.xAxis.formate.center.push(data[i].name)
            transdata.push({org_name: data[i].name,children:[{type: data[i].name, num: data[i].value}]})
          }
          transdata.forEach((d, i) => {
            let obj = {}
            obj.base = d.org_name
            obj.a = d.children
            this.$set(this.cdatas, i, obj)
          })
        }
      }).catch(err => console.error(err))
    },
    getStep (params) {
       // 受攻击阶段数据
      let promise3 = index.getUnderFireAssetStep(params)
      promise3.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          let ob = {name:'root',children:[]}
          ob.children = data
          for(let key in ob){
            this.$set(this.treeMap.data, key, ob[key])
          }
        }
      }).catch(err => console.error(err))
    }
  },
  mounted () {
    let params = {time: this.time}
    this.getState(params)
    this.getTime(params)
    this.getStep(params)

  },
  // 监听
  watch: {
    time (val, oldval) {
      let params = {time: this.time}
      this.getState(params)
      this.getTime(params)
      this.getStep(params)
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.underFireAsset{
  width: 754px;
  height: 412px;
  top: 647px;
  left: 1143px;
  padding: 4px 4px;
}
.piebox{
  width: 266px;
  height: 153px;
  /* border: 1px solid red; */
  position: absolute;
  top:50px;
  left:22px;
}
.pietitle{
  font-size: 12px;
  color: #6ccdff;
  font-family: FZLTHJW;
  text-align: left;
}
.piecontent{
  width: 130px;
  height: 130px;
  /* border: 1px solid red; */
  position: absolute;
  left:25px;
  top:0px;
}
.pielegend{
  /* border: 1px solid red; */
  position: absolute;
  bottom:0;
  right:0;
  width: 70px;
  text-align: left;
}
.border{
  list-style-type: none;
  position: absolute;
  top:47px;
  right:60px;
}
.border li{
  width: 10px;
  height: 5px;
  border: 1px solid #383f4c;
  margin-bottom: 18px;
}
.border li:last-child{
  margin-bottom: 0;
}
.cloumnbox{
  width: 715px;
  height: 163px;
  /* border: 1px solid red; */
  position: absolute;
  top: 227px;
  left: 22px;
}
.cloumntitle{
  font-size: 12px;
  color: #6ccdff;
  font-family: FZLTHJW;
  text-align: left;
}
.treeMapbox{
  width: 387px;
  height:153px;
  position: absolute;
  top: 50px;
  left:330px;
}
.treeMapboxtitle{
  width: 100%;
  height: 30px;
  font-size: 12px;
  color: #6ccdff;
  font-family: FZLTHJW;
  text-align: left;
}
.treeMapboxCenter{
  width: 100%;
  height: calc(100% - 30px);
  border: 1px solid #383f4c;
}
</style>



