/*
 *     Created on 2018-07-18 10:44:37
 *     @author: LJG
 *     @summary: svtcharts-基础-图例组件
 *     @version: v1.0.0
 *     Last Modified: 2018-07-18 10:44:37
 *     Modified By: liujianguo
 */
<template>
  <div class="svt-legend" style="">
    <div class="svt-legendrow" v-for="(arr, index) in data" :key="index" v-if="draw" :style="rows">
      <!-- 图例形状 -->
      <div class="svt-legendcell" :style="[how, chartStyle(index)]"></div>
      <!-- 图例name -->
      <div class="svt-legendcell" :style="[nameStyle]" >
        {{ arr.name }}
      </div>
      <!-- 图例value -->
      <div class="svt-legendcell" :style="[valueStyle]">
        {{ arr.value }}
      </div>
    </div>
  </div>
</template>

<script>
const props = {
  options: {
    type: Object,
    default: {}
  },
  data: {
    type: Array,
    default: ''
  }
}
export default {
  name: 'svt-legend',
  props,
  data () {
    return {
      draw: false
    }
  },
  computed: {
    rows () {
      const { options } = this
      return {
        height: `${options.rowHeight}px`,
        'line-height': `${options.rowHeight}px`
      }
    },
    how () {
      const { grap } = this.options
      if (grap.how === 'circle') {
        return {
          width: `${grap.circle.r * 2}px`,
          height: `${grap.circle.r * 2}px`,
          'border-radius': `${grap.circle.r}px`
        }
      } else if (this.options.grap.how === 'rectangle') {
        return {
          width: `${grap.rectangle.width}px`,
          height: `${grap.rectangle.height}px`,
          border: grap.rectangle.in,
          outline: grap.rectangle.out,
          'vertical-align': 'middle'
        }
      }
    },
    nameStyle () {
      const { name } = this.options
      return {
        width: `${name.width}px`,
        'margin-left': `${name.left}px`,
        'font-size': `${name.fontSize}px`,
        'text-align': name.align,
        'font-family': `${name.fontFamily}`
      }
    },
    valueStyle () {
      const { value } = this.options
      return {
        width: `${value.width}px`,
        'margin-left': `${value.left}px`,
        'font-size': `${value.fontSize}px`,
        'text-align': value.align
      }
    }
  },
  watch: {
    data () {
      if (this.data !== []) {
        this.draw = true
      }
    }
  },
  methods: {
    chartStyle (i) {
      const { grap } = this.options
      return {
        'background-color': grap.color[i]
      }
    }
  }
}
</script>

<style>
.svt-legend{
  color: #ffffff
}
.svt-legendcell{
  display: inline-block;
}
</style>



