 *     @version: v1.0.0
 *     Last Modified: 2018-07-02 21:23:37
 *     Modified By: limeina
 */
<template>
  <div class="svt-tooltip" v-if="show" v-html="data" :style="position"></div>
</template>

<script>
const props = {
  x: {
    type: Number,
    default: 0
  },
  y: {
    type: Number,
    default: 0
  },
  data: {
    type: String,
    default: ''
  }
}
export default {
  name: 'svt-tooltip',
  props,
  data () {
    return {
      positionX: null,
      positionY: null
    }
  },
  computed: {
    show () {
      if (this.data === '') {
        return false
      }
      return true
    },
    position () {
      return {
        top: this.positionY,
        left: this.positionX
      }
    }
  },
  watch: {
    x () {
      if (this.show) {
        this.$nextTick(() => {
          let eleWidth = this.$el.clientWidth
          let parentWidth = this.$el.parentNode.clientWidth
          if (this.x + eleWidth > parentWidth) {
            this.positionX = `${this.x - eleWidth - 20}px`
          } else {
            this.positionX = `${this.x + 10}px`
          }
        })
      }
    },
    y () {
      if (this.show) {
        this.$nextTick(() => {
          let eleHeight = this.$el.clientHeight
          let parentHeight = this.$el.parentNode.clientHeight
          if (this.y + eleHeight > parentHeight) {
            this.positionY = `${this.y - eleHeight - 20}px`
          } else {
            this.positionY = `${this.y + 10}px`
          }
        })
      }
    }
  }
}
</script>

<style>
.svt-tooltip{
  position: absolute;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, .4);
  top: 0;
  left: 0;
  color: white;
  white-space: nowrap;
  z-index: 1
}

.svt-tooltip div{
  font-size: 0;
  text-align: left;
}

.svt-tooltip div > span{
  display: table-cell;
  height: 25px;
  line-height: 25px;
  padding-left: 10px;
  font-size: 15px;
  vertical-align: middle;
}

.svt-tooltip div > span:last-child{
  padding-right: 10px;
}
</style>



