/*
 *    Created on 2018-10-10 13:08:41
 *    @author:Gy
 *    @summary: 三维地图平面模块-区块
 *      1. 支持墨卡托坐标显示
 *      2. 支持将中国摆放在中间的坐标设置
 *    @version:v1.0.0
 *    Last Modified: 2018-10-10 13:08:51
 *    @Modified By:Gy
 */
import {MercatorCoord} from '@/components/charts/svt-mapT/common/mercator'
let THREE = window.THREE
export default class MapBlockA {
  constructor (options = {}) {
    // 着色器
    this.shaders = {
      'vertexShader': `
        attribute vec3 acolor;
        varying vec3 vcolor;
        void main () {
          vcolor = acolor;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
      `,
      'fragmentShader': `
        varying vec3 vcolor;
        uniform float uopacity;
        void main () {
          gl_FragColor.rgb = vcolor;
          gl_FragColor.a = uopacity;
        }
      `
    }
    // 参数设置
    let {
      // 设置最大承受几何点数，当部分几何体没有正常显示时，可能是由于该参数设定的值偏低
      count = 200000,
      // 颜色
      color = '#0E2243',
      // 透明度
      opacity = 1.0,
      // 判断是否将中国置于世界中间
      isChinaCenter = true,
      // 是否变为墨卡托坐标
      isMercator = false,
      // 若开启墨卡托坐标变换, 则开启offsetX, offsetY
      offsetX = 0,
      offsetY = -15
    } = options
    this.color = color
    this.count = count
    this.opacity = opacity
    this.isChinaCenter = isChinaCenter
    this.isMercator = isMercator
    this.offsetX = offsetX
    this.offsetY = offsetY
    // 创建buffer几何体
    let bufferGeometry = new THREE.BufferGeometry()
    let positions = new Float32Array(count * 3)
    let colors = new Float32Array(count * 3)
    bufferGeometry.addAttribute('position', new THREE.BufferAttribute(positions, 3).setDynamic(true))
    bufferGeometry.addAttribute('acolor', new THREE.BufferAttribute(colors, 3).setDynamic(true))
    bufferGeometry.setDrawRange(0, 0)
    let material = new THREE.ShaderMaterial({
      uniforms: {
        uopacity: {type: 'float', value: opacity}
      },
      vertexShader: this.shaders.vertexShader,
      fragmentShader: this.shaders.fragmentShader,
      transparent: true,
      side: THREE.DoubleSide
    })
    this.mesh = new THREE.Mesh(bufferGeometry, material)
  }
  // 更新地图模板
  update (data) {
    let areas, type, coordinates, name, shapesAreas, coords, shapePts, coord, _coords, color, id
    let positions = this.mesh.geometry.attributes.position.array
    let colors = this.mesh.geometry.attributes.acolor.array
    let idsList = []
    let cnt = 0
    for (let i = 0, il = data.length; i < il; i++) {
      areas = data[i]
      type = areas.geometry.type
      coordinates = areas.geometry.coordinates
      name = areas.properties.name
      id = areas.id
      shapesAreas = []
      if (type === 'Polygon') {
        for (let j = 0, jl = coordinates.length; j < jl; j++) {
          coords = coordinates[j]
          shapePts = []
          for (let k = 0, kl = coords.length; k < kl; k++) {
            coord = coords[k]
            if (coord[0] >= -30 && name !== 'Greenland' && this.isChinaCenter) {
              coord = [coord[0] - 360, coord[1]]
              if (this.isMercator) {
                coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
              }
              shapePts.push(new THREE.Vector2(coord[0], coord[1]))
            } else {
              if (this.isMercator) {
                coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
              }
              shapePts.push(new THREE.Vector2(coord[0], coord[1]))
            }
          }
          let shape = new THREE.Shape(shapePts)
          shapesAreas.push(shape)
        }
      } else if (type === 'MultiPolygon') {
        for (let j = 0, jl = coordinates.length; j < jl; j++) {
          coords = coordinates[j]
          for (let k = 0, kl = coords.length; k < kl; k++) {
            _coords = coords[k]
            shapePts = []
            for (let m = 0, ml = _coords.length; m < ml; m++) {
              coord = _coords[m]
              if (coord[0] >= -30 && name !== 'Greenland' && this.isChinaCenter) {
                coord = [coord[0] - 360, coord[1]]
                if (this.isMercator) {
                  coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
                }
                shapePts.push(new THREE.Vector2(coord[0], coord[1]))
              } else {
                if (this.isMercator) {
                  coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
                }
                shapePts.push(new THREE.Vector2(coord[0], coord[1]))
              }
            }
            let shape = new THREE.Shape(shapePts)
            shapesAreas.push(shape)
          }
        }
      }
      color = new THREE.Color(this.color)
      if (shapesAreas.length > 0) {
        let geometry = new THREE.ShapeGeometry(shapesAreas)
        geometry.faces.forEach(function (face) {
          positions[cnt * 9 + 0] = geometry.vertices[face.a].x
          positions[cnt * 9 + 1] = geometry.vertices[face.a].y
          positions[cnt * 9 + 2] = geometry.vertices[face.a].z
          positions[cnt * 9 + 3] = geometry.vertices[face.b].x
          positions[cnt * 9 + 4] = geometry.vertices[face.b].y
          positions[cnt * 9 + 5] = geometry.vertices[face.b].z
          positions[cnt * 9 + 6] = geometry.vertices[face.c].x
          positions[cnt * 9 + 7] = geometry.vertices[face.c].y
          positions[cnt * 9 + 8] = geometry.vertices[face.c].z
          colors[cnt * 9 + 0] = color.r
          colors[cnt * 9 + 1] = color.g
          colors[cnt * 9 + 2] = color.b
          colors[cnt * 9 + 3] = color.r
          colors[cnt * 9 + 4] = color.g
          colors[cnt * 9 + 5] = color.b
          colors[cnt * 9 + 6] = color.r
          colors[cnt * 9 + 7] = color.g
          colors[cnt * 9 + 8] = color.b
          idsList[cnt * 3 + 0] = name
          idsList[cnt * 3 + 1] = name
          idsList[cnt * 3 + 2] = name
          cnt++
        })
      }
    }
    this.mesh.geometry.attributes.position.needsUpdate = true
    this.mesh.geometry.attributes.acolor.needsUpdate = true
    this.mesh.geometry.idsList = idsList
    this.mesh.geometry.setDrawRange(0, cnt * 3)
    this.mesh.geometry.computeBoundingSphere()
  }
  // 区域着色
  /**
   *  areaId : 地区编号，当为'all'时，则为全部模型变颜色
   *  color : 颜色
   */
  setAreaColor (options = {}) {
    let {
      areaId = undefined,
      color = 0x0088ee
    } = options
    if (areaId === undefined) return
    let idsList = this.mesh.geometry.idsList
    let colors = this.mesh.geometry.attributes.acolor.array
    let c = new THREE.Color(color)
    for (let i = 0; i < idsList.length; i++) {
      if (areaId === idsList[i]) {
        colors[i * 3] = c.r
        colors[i * 3 + 1] = c.g
        colors[i * 3 + 2] = c.b
      }
      if (areaId === 'all') {
        colors[i * 3] = c.r
        colors[i * 3 + 1] = c.g
        colors[i * 3 + 2] = c.b
      }
    }
    this.mesh.geometry.attributes.acolor.needsUpdate = true
  }
  show () {
    this.mesh.visible = true
  }
  hide () {
    this.mesh.visible = false
  }
}



