<template>
  <div class = "sz-title">
    <div class="title-content">网络安全态势<i class="fas fa-undo-alt"></i></div>
    <div class="select">
      <div class="text">{{ text }}</div>
      <div class="choice" @click.stop="isShow">
        <img src="../../assets/images/time.png">
        <img :src="imgUrl">
      </div>
      <ul class="choice-content" v-show="isShowselect">
        <li v-for="(item, index) in selectitem" :key="index" @click="check(index)" v-bind:class="{titlecolor:index==current}">{{ item }}</li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'szTitle',
  components: {
  },
  props: {},
  data () {
    return {
      selectitem: ['今天', '本周', '本月', '本季度', '本年'],
      text: '本季度',
      current: 3,
      isShowselect: false
    }
  },
  // 计算变量
  computed: {
    imgUrl () {
      if (this.isShowselect === false) {
        return require('../../assets/images/time_down.png')
      } else if (this.isShowselect === true) {
        return require('../../assets/images/time_up.png')
      }
    }
  },
  // 方法
  methods: {
    // 重置组件size函数
    onResize () {
      this.width = this.$el.querySelector('.kjb-warningTotal-level').clientWidth
      this.height = this.$el.querySelector('.kjb-warningTotal-level').clientHeight
    },
    // 监听容器大小改变
    onClose () {
      this.onResize()
    },
    isShow () {
      this.isShowselect = !this.isShowselect
    },
    check (index) {
      this.current = index
      this.text = this.selectitem[index]
      this.$store.commit('setTime', this.selectitem[index])
    }
  },
  // 监听
  watch: {
    // 监听容器大小改变
    close () {
      this.onResize()
    }
  },
  // 更新之后执行
  updated () {
    this.isLoad = false
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* *{
  margin: 0;
  padding: 0;
} */
/* @font-face{
  font-family: FZLTHJW;
  src: url("../../assets/fonts/FZLTHJWGB10.woff")
} */
.sz-title{
  width: 100%;
  height: 79px;
  /* background: rgba(255,0,0,0.5); */
  position: relative;
  border-bottom: 1px solid #18384F;
}
.title-content{
  font-size: 24px;
  color: #d2d8de;
  font-family: FZLTHJW;
  text-align: left;
  position: absolute;
  top:30px;
  left:15px;
  height: 24px;
  line-height: 24px;
  /* border: 1px solid red; */
}
.title-content i{
  font-size: 16px;
  margin-left: 14px;
  cursor: pointer;
  color: #5a9ace;
 }
.title-content::before{
  content: '';
  display: block;
  width: 4px;
  height: 25px;
  background-color: #5a9ace;
  float: left;
  margin-right: 10px;
  margin-top: -1px;
}
.select{
  position: absolute;
  top:30px;
  right:15px;
}
.select::after{
  content: '';
  display: block;
  clear: both;
}
.text{
  height: 35px;
  width: 141px;
  background-color: #163049;
  float: left;
  margin-right: 3px;
  line-height: 35px;
  text-align: left;
  text-indent: 14px;
  font-size: 16px;
  color: #ffffff;
  font-family: FZLTHJW;
}
.choice{
  float: left;
  height: 35px;
  line-height: 35px;
  width: 52px;
  background-color:#2c4a66;
  cursor: pointer;
}
.choice img{
  vertical-align: middle;
}
.choice img:nth-child(1){
  margin-right: 9px;
  margin-left: 3px;
}
.choice-content{
  list-style-type: none;
  width: 108px;
  height: 199px;
  position: absolute;
  border: 1px solid #3a556e;
  background-color:#2c4a66;
  position: absolute;
  top:23px;
  left:86px;
  padding: 0;
  overflow: hidden;
  z-index: 2
}
.choice-content li{
  height: 39px;
  line-height: 40px;
  padding-left: 20px;
  font-family: FZLTHJW;
  font-size: 16px;
  color: #e0e0e0;
  text-align: left;
  border-bottom: 1px solid #163049;
  cursor: pointer;
}
.choice-content li.titlecolor{
  background-color: #3a8ed3;
}
</style>



