/*
 *    Created on 2018-10-31 21:23:13
 *    @author:Gy
 *    @summary: 三维地图平面-经纬度二维坐标变换方法-通用
 *        1. 支持墨卡托坐标变换
 *        2. 支持中国摆放在世界地图中间位置
 *    @version:v1.0.0
 *    Last Modified: 2018-10-31 21:23:13
 *    @Modified By:Gy
 */
import {MercatorCoord} from '@/components/charts/svt-mapT/common/mercator'
function coordTransform (options = {}) {
  let {
    // 经纬度坐标
    coord = [114.51, 38.01],
    // 判断是否将中国置于世界中间
    isChinaCenter = true,
    // 判断是否变为墨卡托坐标
    isMercator = false,
    // 若开启墨卡托坐标变换，则开启offsetX, offsetY
    offsetX = 0, // 经度偏移量
    offsetY = -15 // 纬度偏移量
  } = options
  // 初始化经纬度
  let lng = Number(coord[0])
  let lat = Number(coord[1])
  let newCoord = [lng, lat]
  if (isChinaCenter && lng >= -30) {
    lng -= 360
  }
  if (isMercator) {
    newCoord = MercatorCoord(lng + offsetX, lat + offsetY)
  }
  return newCoord
}
export { coordTransform }



