/*
 *    Created on 2018-10-31 19:59:01
 *    @author:Gy
 *    @summary: 三维地图平面模块-区块
 *      1. 支持墨卡托坐标显示
 *      2. 支持将中国摆放在中间的坐标设置
 *    @version:v1.0.0
 *    Last Modified: 2018-10-31 19:59:01
 *    @Modified By:Gy
 */
import {MercatorCoord} from '@/components/charts/svt-mapT/common/mercator.js'
let THREE = window.THREE
export default class mapLineA {
  constructor (options = {}) {
    // 着色器
    this.shaders = {
      'vertexShader': `
        attribute vec3 acolor;
        varying vec3 vcolor;
        void main () {
          vcolor = acolor;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
      `,
      'fragmentShader': `
        varying vec3 vcolor;
        uniform float uopacity;
        void main () {
          gl_FragColor.rgb = vcolor;
          gl_FragColor.a = uopacity;
        }
      `
    }
    let {
      // 设置最大承受几何点数，当部分几何体没有正常显示时，可能是由于该参数设定的值偏低
      count = 200000,
      // 颜色
      color = '#5088D2',
      // 透明度
      opacity = 1.0,
      // 判断是否将中国置于世界中间
      isChinaCenter = true,
      // 是否变为墨卡托坐标
      isMercator = false,
      // 若开启墨卡托坐标变换, 则开启offsetX, offsetY
      offsetX = 0,
      offsetY = -15
    } = options
    this.color = color
    this.count = count
    this.opacity = opacity
    this.isChinaCenter = isChinaCenter
    this.isMercator = isMercator
    this.offsetX = offsetX
    this.offsetY = offsetY
    let bufferMergeGeometry = new THREE.BufferGeometry()
    let positions = new Float32Array(count * 3)
    let colors = new Float32Array(count * 3)
    bufferMergeGeometry.addAttribute('position', new THREE.BufferAttribute(positions, 3).setDynamic(true))
    bufferMergeGeometry.addAttribute('acolor', new THREE.BufferAttribute(colors, 3).setDynamic(true))
    bufferMergeGeometry.setDrawRange(0, 0)
    let material = new THREE.ShaderMaterial({
      uniforms: {
        uopacity: {type: 'float', value: opacity}
      },
      vertexShader: this.shaders.vertexShader,
      fragmentShader: this.shaders.fragmentShader,
      transparent: true,
      side: THREE.DoubleSide
    })
    this.mesh = new THREE.LineSegments(bufferMergeGeometry, material)
  }
  // 更新地图模板
  update (data) {
    let self = this
    let areas, type, coordinates, name, coords, coord, _coords, color, id
    let positions = self.mesh.geometry.attributes.position.array
    let colors = self.mesh.geometry.attributes.acolor.array
    let idsList = []
    let curElement = 0
    color = new THREE.Color(this.color)
    for (let i = 0, il = data.length; i < il; i++) {
      areas = data[i]
      type = areas.geometry.type
      coordinates = areas.geometry.coordinates
      name = areas.properties.name
      id = areas.id
      if (type === 'Polygon') {
        for (let j = 0, jl = coordinates.length; j < jl; j++) {
          coords = coordinates[j]
          for (let k = 0, kl = coords.length; k < kl; k++) {
            coord = coords[k]
            let vertex
            if (coord[0] >= -30 && name !== 'Greenland' && this.isChinaCenter) {
              coord = [coord[0] - 360, coord[1]]
              if (this.isMercator) {
                coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
              }
              vertex = new THREE.Vector3(coord[0], coord[1], 1)
            } else {
              if (this.isMercator) {
                coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
              }
              vertex = new THREE.Vector3(coord[0], coord[1], 1)
            }
            switch (k) {
              case 0:
                positions[curElement * 3 + 0] = vertex.x
                positions[curElement * 3 + 1] = vertex.y
                positions[curElement * 3 + 2] = vertex.z
                colors[curElement * 3 + 0] = color.r
                colors[curElement * 3 + 1] = color.g
                colors[curElement * 3 + 2] = color.b
                idsList[curElement] = name
                curElement++
                break
              case kl - 1:
                positions[curElement * 3 + 0] = vertex.x
                positions[curElement * 3 + 1] = vertex.y
                positions[curElement * 3 + 2] = vertex.z
                colors[curElement * 3 + 0] = color.r
                colors[curElement * 3 + 1] = color.g
                colors[curElement * 3 + 2] = color.b
                idsList[curElement] = name
                curElement++
                break
              default:
                positions[curElement * 3 + 0] = vertex.x
                positions[curElement * 3 + 1] = vertex.y
                positions[curElement * 3 + 2] = vertex.z
                colors[curElement * 3 + 0] = color.r
                colors[curElement * 3 + 1] = color.g
                colors[curElement * 3 + 2] = color.b
                idsList[curElement] = name
                curElement++
                positions[curElement * 3 + 0] = vertex.x
                positions[curElement * 3 + 1] = vertex.y
                positions[curElement * 3 + 2] = vertex.z
                colors[curElement * 3 + 0] = color.r
                colors[curElement * 3 + 1] = color.g
                colors[curElement * 3 + 2] = color.b
                idsList[curElement] = name
                curElement++
                break
            }
          }
        }
      } else if (type === 'MultiPolygon') {
        for (let j = 0, jl = coordinates.length; j < jl; j++) {
          coords = coordinates[j]
          for (let k = 0, kl = coords.length; k < kl; k++) {
            _coords = coords[k]
            for (let m = 0, ml = _coords.length; m < ml; m++) {
              coord = _coords[m]
              let vertex
              if (coord[0] >= -30 && name !== 'Greenland' && this.isChinaCenter) {
                coord = [coord[0] - 360, coord[1]]
                if (this.isMercator) {
                  coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
                }
                vertex = new THREE.Vector3(coord[0], coord[1], 1)
              } else {
                if (this.isMercator) {
                  coord = MercatorCoord(coord[0] + this.offsetX, coord[1] + this.offsetY)
                }
                vertex = new THREE.Vector3(coord[0], coord[1], 1)
              }
              switch (m) {
                case 0:
                  positions[curElement * 3 + 0] = vertex.x
                  positions[curElement * 3 + 1] = vertex.y
                  positions[curElement * 3 + 2] = vertex.z
                  colors[curElement * 3 + 0] = color.r
                  colors[curElement * 3 + 1] = color.g
                  colors[curElement * 3 + 2] = color.b
                  idsList[curElement] = name
                  curElement++
                  break
                case ml - 1:
                  positions[curElement * 3 + 0] = vertex.x
                  positions[curElement * 3 + 1] = vertex.y
                  positions[curElement * 3 + 2] = vertex.z
                  colors[curElement * 3 + 0] = color.r
                  colors[curElement * 3 + 1] = color.g
                  colors[curElement * 3 + 2] = color.b
                  idsList[curElement] = name
                  curElement++
                  break
                default:
                  positions[curElement * 3 + 0] = vertex.x
                  positions[curElement * 3 + 1] = vertex.y
                  positions[curElement * 3 + 2] = vertex.z
                  colors[curElement * 3 + 0] = color.r
                  colors[curElement * 3 + 1] = color.g
                  colors[curElement * 3 + 2] = color.b
                  idsList[curElement] = name
                  curElement++
                  positions[curElement * 3 + 0] = vertex.x
                  positions[curElement * 3 + 1] = vertex.y
                  positions[curElement * 3 + 2] = vertex.z
                  colors[curElement * 3 + 0] = color.r
                  colors[curElement * 3 + 1] = color.g
                  colors[curElement * 3 + 2] = color.b
                  idsList[curElement] = name
                  curElement++
                  break
              }
            }
          }
        }
      }
    }
    this.mesh.geometry.attributes.position.needsUpdate = true
    this.mesh.geometry.attributes.acolor.needsUpdate = true
    this.mesh.geometry.idsList = idsList
    this.mesh.geometry.setDrawRange(0, curElement * 3)
    this.mesh.geometry.computeBoundingSphere()
  }
  // 区域着色
  /**
   *  areaId : 地区编号，当为'all'时，则为全部模型变颜色
   *  color : 颜色
   */
  // 区域着色
  setAreaColor (options = {}) {
    let {
      areaId = undefined,
      color = 0xffffff
    } = options
    let toColor = new THREE.Color(color)
    if (areaId === undefined) return
    let idsList = this.mesh.geometry.idsList
    let colors = this.mesh.geometry.attributes.acolor.array
    let positions = this.mesh.geometry.attributes.position.array
    if (areaId === 'all') {
      for (let i = 0; i < idsList.length; i++) {
        colors[i * 3] = toColor.r
        colors[i * 3 + 1] = toColor.g
        colors[i * 3 + 2] = toColor.b
        positions[i * 3 + 2] = 1
      }
    } else {
      for (let i = 0; i < idsList.length; i++) {
        if (areaId === idsList[i]) {
          colors[i * 3] = toColor.r
          colors[i * 3 + 1] = toColor.g
          colors[i * 3 + 2] = toColor.b
          positions[i * 3 + 2] = 1.2
        }
      }
    }
    this.mesh.geometry.attributes.acolor.needsUpdate = true
    this.mesh.geometry.attributes.position.needsUpdate = true
  }
  show () {
    this.mesh.visible = true
  }
  hide () {
    this.mesh.visible = false
  }
}



