import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
Vue.prototype.$ajax = axios

const store = new Vuex.Store({
  // 定义状态
  state: {
    // day 今天,week 本周,month 本月,quarter 本季度,year 本年
    time:'quarter'
  },
  mutations: {
    setTime(state, t) {
      let ob = {
        '今天': 'day',
        '本周': 'week',
        '本月': 'month',
        '本季度': 'quarter',
        '本年': 'year'
      }
      state.time = ob[t]
    }
  },
  actions: {
  }
})

export default store



