<template>
  <div class="sz-module threatEvent">
    <div class="sz-module-title">
        威胁事件
    </div>
    <div class = "sz-module-center">
      <div class="piebox">
        <div class="pietitle">威胁等级占比</div>
        <div class="piecontent">
          <svt-pie :data="data" :class="classname" :options="options" :width="width" :height="height"></svt-pie>
        </div>
      </div>
      <div class="linebox">
        <div class="linetitle">事件发生数量趋势</div>
        <div class="linecontent">
          <svt-line :width="lwidth" :height="lheight" :options="loptions" :datas="ldata"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { index } from '@/api'
import svtPie from '@/components/charts/svt-pie/svt-pie'
import svtLine from '@/components/charts/svt-line/svt-line'
export default {
  name: 'threatEvent',
  components: {
    svtPie,
    svtLine
  },
  props: {},
  data () {
    return {
      classname: 'pie2',
      // 容器宽度
      width: 250,
      // 容器高度
      height: 180,
      // 配置项
      options: {
        // 悬浮提示数值还是百分比 true为数值，false为百分比
        tooltiptype: false,
        // type1正常 type2花瓣图
        type: 'type2',
        // 外圆弧半径
        outerRadius: 68,
        // 内圆弧半径
        innerRadius: 0,
        // 上边距
        magrinTop: 0,
        // 下边距
        magrinBottom: 0,
        // 左边距
        magrinLeft: 0,
        // 右边距
        magrinRight: 0,
        // 文字字号
        fontSize: 12,
        fontFamily: 'FZLTHJW',
        // 速度
        speed: 700,
        // color
        color: ['#7ed5fb', '#ffde00', '#ff7200'],
        typeName: ['高危', '中危', '低危'],
        shadow: {
          show: true,
          outerRadius: 70,
          width: 100,
          innerRadius: 20,
          color: 'rgba(25, 94, 123, 0.2)'
        },
        text: {
          show: true,
          porotor: true,
          color: '#fff'
        },
        line: {
          color: '#ccc'
        },
        tooltip: {
          unit: '件'
        }
      },
      data: [],
      // 展示页标题
      name: '饼图',
      // input行统一样式，必须写
      labelPosition: 'right',
      values: [],
      names: [],
      // 折线图----------------------------------
      // 轴方向
      axisOrientX: 'axisBottom',
      axisOrientY: 'axisLeft',
      lwidth: 476,
      lheight: 155,
      loptions: {
        width: 476,
        height: 155,
        margin: [25, 20, 25, 50],
        // curve曲线,normal正常线
        type: 'normal',
        axise: {
          // 坐标轴颜色
          color: '#495d78',
          // 坐标轴文字样式
          text: {
            fontFamily: 'FZLTHJW',
            fontSize: 14,
            color: '#fff'
          },
          // 横轴配置项
          xAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: true,
            // 刻度长线是否展示
            tickLine: {
              show: false,
              // 线形：dotted虚线，solid实线
              linear: 'dotted'
            },
            // 自定义横轴显示的信息
            // 注意：使用了横轴显示信息后，center数组的数量应该与数据的条数一样多
            // 不需要显示的使用空字符串
            formate: {
              show: false,
              center: ['2018.10.09', '', '0', '2', '0', '3', '0', '4', '', '5', '', '6', '', '7', '', '8']
            }
          },
          yAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: true,
            // 刻度长线是否展示
            tickLine: {
              show: true,
              // 线形：dotted虚线，solid实线
              linear: 'dotted'
            },
            // 设置纵轴显示的刻度数
            ticks: 4
          }
        },
        line: {
          // 是否展示折线
          show: true,
          // 折线线的颜色
          stroke: ['#7ed5fb', 'red']
        },
        area: {
          show: true,
          fill: ['rgba(57, 87, 111, .3)', 'rgba(10,10,10,0.5)']
        },
        circle: {
          show: true,
          stroke: ['#7ed5fb', '#fff'],
          fill: ['#061a2b', 'red'],
          r: 4
        },
        tooltip: {
          show: true,
          lineColor: '#8ca7b7',
          unit: '件'
        },
        // 分页部分
        // pageSize是每页显示条数
        // 不需要分页显示时可以将
        pageSize: 7,
        // pageNum是当前在什么页码上
        pageNum: 1

      },
      ldata: [],
      // logsnum: 0,
      // timeArr: [],
      datas: []
    }
  },
  // 计算变量
  computed: {
    // 获得格式化组件数据内容
    pieOptions () {
      return JSON.stringify(this.options, null, 2)
    },
    // 获得格式化组件配置内容
    pieData () {
      return JSON.stringify(this.data, null, 2)
    },
    // 图例展示数据
    dataLegend () {
      let legendArr = []
      for (let i = 0; i < this.names.length; i++) {
        legendArr.push({name: this.names[i]})
        // this.computedwarnnum()
      }
      return legendArr
    },
    // 全局时间轴选中的时间
    time () {
      return this.$store.state.time
    }
  },
  // 方法
  methods: {
    getType (params) {
      let promise = index.getThreatEventType(params)
      promise.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          this.data = data
          for (let i = 0; i < this.data.length; i++) {
            this.$set(this.names, i, this.data[i].name)
            this.$set(this.values, i, this.data[i].value)
          }
        } else {
          // let valueArr = ['失陷', '疑似失陷', '未失陷', '未知']
          for (let i = 0; i < 4; i++) {
            // this.$set(this.names, i, valueArr[i])
            this.$set(this.values, i, 0)
          }
        }
      }).catch(err => console.error(err))
    },
    getTrend (params) {
      let promise2 = index.getThreatEventTrend(params)
      promise2.then((res) => {
        if (res.data.data.length > 0) {
          let data = res.data.data
          let transdata = []
          for (let i = 0; i < data.length; i++) {
            transdata.push({name: data[i].name, value: [data[i].value]})
          }
          this.ldata = transdata
        }
      }).catch(err => console.error(err))
    }
  },
  mounted () {
    let params = {time: this.time}
    this.getType(params)
    this.getTrend(params)

  },
  // 监听
  watch: {
    time (val, oldval) {
      let params = {time: this.time}
      this.getType(params)
      this.getTrend(params)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.threatEvent{
  width: 754px;
  height: 229px;
  top: 101px;
  left: 1143px;
  padding: 4px 4px;
}
.piebox{
  width: 183px;
  height: 166px;
  /* border: 1px solid red; */
  position: absolute;
  top:50px;
  left:30px;
}
.pietitle,.linetitle{
  font-size: 12px;
  color: #6ccdff;
  font-family: FZLTHJW;
  text-align: left;
}
.piecontent{
  width: 136px;
  height: 136px;
  /* border: 1px solid red; */
  position: absolute;
  left:-22px;
  bottom:20px;
}
.linebox{
  width: 476px;
  height: 165px;
  /* border: 1px solid red; */
  position: absolute;
  top:50px;
  right:22px;
}
.linecontent{
  position: relative;
}
/* 隐藏刻度线 */
.linecontent #svt-line .tick line{
  display: none;
}
/* 设置坐标轴颜色 */
.linecontent #svt-line .domain{
  stroke:rgb(0, 203, 255, 0.3);
  stroke-width: 1px;
}
/* 刻度值样式 */
.linecontent #svt-line .tick text{
  fill:#b7effd;
  font-size: 12px;
  font-family: 'PRegular';
}
/* 提示框线样式 */
.tooltip-line{
  /* 将原div背景色设为透明，再添加border */
  /* border-left: 1px dashed  rgba(250,192,68,.6); */
  background: linear-gradient(to bottom, rgba(250,192,68,.6), rgba(250,192,68,.6) 7px, transparent 3px, transparent) !important;
  background-size: 100% 10px !important;
}
/* 折线样式 */
#svt-line g[style] path{
  stroke-width: 2px;
}
/* 节点圆样式 */
#svt-line g[style] g circle{
  stroke-width: 2px;
}
/* 隐藏横坐标的刻度值 */
#svt-line g:nth-child(1) g text{
  display:none;
}
</style>



