<template>
  <div class = "sz-container" @click="isnotShowselect">
    <szTitle ref="szTitle"></szTitle>
    <threatEvent></threatEvent>
    <safeEmerge></safeEmerge>
    <aggressor></aggressor>
    <underFireAsset></underFireAsset>
    <list></list>
  </div>
</template>

<script>
import szTitle from '@/components/modules/title.vue'
import threatEvent from '@/components/modules/threat-event.vue'
import safeEmerge from '@/components/modules/safe-emerge.vue'
import aggressor from '@/components/modules/aggressor.vue'
import underFireAsset from '@/components/modules/under-fire-asset.vue'
import list from '@/components/modules/list.vue'
export default {
  name: 'App',
  data () {
    return {

    }
  },
  computed: {

  },
  methods: {
    isnotShowselect () {
      this.$refs.szTitle.isShowselect = false
    }
  },
  components: {
    szTitle,
    threatEvent,
    safeEmerge,
    aggressor,
    underFireAsset,
    list
  },
  beforeCreate () {
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.sz-container{
  width: 100%;
  height: 100%;
  background: url('~@/assets/images/bg.png')
}
</style>



