<template>
  <div class="charts" :class="classname" :style="circlemargin()" id="piechart">
    <svg :width="width" :height="height" >
      <g class="main" :transform="pieTransform()">
        <circle cx="0" cy="0" :r="options.shadow.outerRadius - options.shadow.width/2" :stroke="options.shadow.color"
                :stroke-width="options.shadow.width" fill="none"/>
        <g class="slices">
          <path
            class="svtPie-ground"
            v-for="(arr,index) of pieData()"
            :key="index"
            :fill="getColor(index)"
            :dex="arr.value"
            :d="arcDraw(index)"
            ref="svtPie"
            @mousemove="onMove($event, arr, index)"
            @mouseout="onOut"
          >{{arr.value}}</path>
          <path></path>
        </g>
        <g class="lines" v-show="options.text.show">
          <polyline v-for="(arr,index) of pieData()"
                    :key="index"
                    :points="textStartOrEnd(index).linepoints"
                    fill="none"
                    :stroke="options.line.color"
                    style=""></polyline>
        </g>
        <g class="labels" v-show="options.text.show">
          <text v-for="(arr,index) of data"
                :key="index"
                dy="0.35em"
                :font-size="options.fontSize"
                :fill="getColor(index)"
                :transform="textStartOrEnd(index).transform"
                :style="textStartOrEnd(index).position"
                :font-family='options.fontFamily'
          >{{arr.name}}</text>
        </g>

      </g>
    </svg>
    <svt-tooltip :data="mousePosition.value" :x="mousePosition.x" :y="mousePosition.y" v-show="mouse"/>
  </div>
</template>
<script>
/* eslint-disable func-call-spacing,no-unexpected-multiline */
import * as d3 from 'd3'
import svtTooltip from './../svt-common/svt-tooltip'
// 继承父元素属性
const props = {
  // 组件宽
  width: {
    type: Number,
    default: window.innerWidth
  },
  // 组件高
  height: {
    type: Number,
    default: window.innerHeight
  },
  classname: {
    type: String,
    default: ''
  },
  // 组件数据
  data: {
    type: Array,
    default: () => []
  },
  // 组件配置项
  options: {
    type: Object,
    default: () => {
      return {
        // 左右间距
        margin: 25,
        // 填充颜色
        color: [
          'rgb(166, 122, 255)',
          'rgb(42, 170, 254)',
          'rgb(41, 198, 174)',
          'rgb(247, 171, 51)',
          'rgb(244, 78, 78)'
        ],
        // 数据最大值
        max: 500,
        // 数据最小值
        min: 0
      }
    }
  }
}
export default {
  components: {
    svtTooltip
  },
  data () {
    return {
      // 提示框位置X
      tooltipX: 0,
      // 提示框位置Y
      tooltipY: 0,
      // 提示框显示内容（html格式）
      tooltipData: '',
      mouse:true,
      mousePosition: {
        x: 0,
        y: 0,
        value:''
      }
    }
  },
  props,
  name: 'svt-pie',
  mounted () {
    this.animationPie()
  },
  methods: {
    onMove (event, arr, index) {
        this.mouse = true
        let pLeft = event.currentTarget.parentElement.parentElement.getBoundingClientRect().left
        let pTop = event.currentTarget.parentElement.parentElement.getBoundingClientRect().top
        this.mousePosition.x = event.clientX - pLeft + 20
        this.mousePosition.y = event.clientY - pTop + 50
        this.mousePosition.value = `
              <div>
                <span style="font-size: 0">
                  <span style="
                      display: inline-block;
                      width: 12px;
                      height: 12px;
                      border-radius: 12px;
                      vertical-align: middle;
                      background-color: ${this.options.color[index]};"></span>
                </span>
                <span>${arr.data.name + '：' + arr.data.value + ' ' + this.options.tooltip.unit}</span>
              </div>
            `
    },
    onOut () {
      this.mouse = false
    },
    pieTransform () {
      return `translate( ${this.width / 2} ,${this.options.outerRadius + this.options.fontSize})`
    },
    getColor (idx) {
      // 生成颜色
      const palette = this.options.color
      return palette[idx % palette.length]
    },
    midAngel (d) {
      return d.startAngle + (d.endAngle - d.startAngle) / 2
    },
    pieData () {
      const speed = this.options.speed
      let plus = 0
      this.data.forEach(function (d, i) {
        plus += d.value
      })
      const pie = d3.pie().sort(null).value(function (d) { return d.value })
      // 根据D3V5的饼图生成器，转换数据
      pie(this.data)
      const piedata = pie(this.data)
      let sum = 0
      piedata.forEach(function (d, i) {
        d._endAngle = d.endAngle
        d.endAngle = d.startAngle
        // d.endAngle = d.endAngle
        d.duration = speed * (d.data.value / plus)
        d.delaytime = sum
        sum += d.duration
      })
      return piedata
    },
    pieEndData () {
      const speed = this.options.speed
      let plus = 0
      this.data.forEach(function (d, i) {
        plus += d.value
      })
      const pie = d3.pie().sort(null).value(function (d) { return d.value })
      // 根据D3V5的饼图生成器，转换数据
      pie(this.data)
      const piedata = pie(this.data)
      let sum = 0
      piedata.forEach(function (d, i) {
        d._endAngle = d.endAngle
        // d.endAngle = d.startAngle
        d.endAngle = d.endAngle
        d.duration = speed * (d.data.value / plus)
        d.delaytime = sum
        sum += d.duration
      })
      return piedata
    },
    maxData (index) {
      const arr = []
      this.pieData().forEach(function (d, i) {
        arr[i] = d.value
      })
      const max = Math.max(...arr)
      return this.pieData()[index].value / max
    },
    // 饼图的类型
    arcType (index) {
      const { outerRadius } = this.options
      let radius
      switch (this.options.type) {
        case 'type1':
          radius = outerRadius
          break
        case 'type2':
          radius = this.maxData(index) * outerRadius
          break
      }
      return radius
    },
    arcDraw (index) {
      const { innerRadius } = this.options
      const arc = d3.arc().innerRadius(innerRadius).outerRadius(this.arcType(index))
      return arc(this.pieData()[index])
    },
    textStartOrEnd (index) {
      const { outerRadius } = this.options
      let d = this.pieEndData()[index]
      // 文字圆弧
      const outerArc = d3.arc().innerRadius(1.2 * outerRadius).outerRadius(1.2 * outerRadius)
      // 线的圆弧
      const oArc = d3.arc().innerRadius(1.1 * outerRadius).outerRadius(1.1 * outerRadius)
      let pos = outerArc.centroid(d)
      // 判断文字的左右边
      const position = this.midAngel(d) < Math.PI ? 'start' : 'end'
      // 判断文字位置
      pos[0] = outerRadius * (this.midAngel(d) < Math.PI ? 1.15 : -1.15)
      // let posArr = []
      // let posx = pos[0] - 40
      // let posy = pos[1] -10
      // posArr.push(posx)
      // posArr.push(posy)
      // 线的位置
      const points = [oArc.centroid(d), outerArc.centroid(d), pos]
      return {
        position: `text-anchor: ${position}`,
        transform: `translate(${pos})`,
        linepoints: points
      }
    },
    // 动画过渡
    tweenArc (fn) {
      let max = this.arcType
      const { innerRadius } = this.options
      return function (a, i) {
        const arc = d3.arc().innerRadius(innerRadius).outerRadius(max(i))
        let d = fn.call(this, a, i)
        return function (t) {
          return arc(d3.interpolate(a, d)(t))
        }
      }
    },
    // 旋转动画
    animationPie () {
      this.$nextTick(function () {
        let pieTran
        const className = this.$el.getAttribute('class').split(' ')[1]
        const pieDom = d3.selectAll(`.${className} path.svtPie-ground`)
        pieDom.interrupt()
        if (pieTran) {
          pieDom.transition.each('end')
        }
        pieDom.data(this.pieData())
        pieTran = pieDom.transition()
        pieTran.ease(d3.easeLinear)
        pieTran.duration(function (d) {
          return d.duration
        })
          .delay(function (d) {
            return d.delaytime
          })
          .attrTween('d', this.tweenArc
          (function (d, i) {
            return {
              startAngle: d.startAngle,
              endAngle: d._endAngle
            }
          }))
      })
    },
    circlemargin () {
      return `margin-top:${this.options.magrinTop}px;margin-left:${this.options.magrinLeft}px;margin-bottom:${this.options.magrinBottom}px;margin-right:${this.options.magrinRight}px`
    }
  },
  watch: {
    'data': {
      handler () {
        if (this.data !== []) {
          this.animationPie()
        }
      },
      deep: true
    },
    'options': {
      handler () {
        let timer = null
        clearTimeout(timer)
        timer = setTimeout(this.animationPie(), 300)
      },
      deep: true
    }
  }
}

</script>
<style scoped>
  .charts {
    /* margin: 0px auto; */
    /* margin-top: 12px;
    margin-left: -6px;*/
   }
</style>



