let THREE = window.THREE
export default class EffectMapPointA {
  constructor (options = {}) {
    let {
      // 颜色
      color = 0xffffff * Math.random(),
      // 透明度
      opacity = 1,
      // 尺寸
      size = 1,
      // 纹理
      map = null
    } = options
    this.color = color
    this.opacity = opacity
    this.size = size
    this.map = map
    this.mesh = new THREE.Sprite(new THREE.SpriteMaterial({
      color: new THREE.Color(this.color),
      transparent: true,
      opacity: this.opacity,
      map: this.map
    }))
    this.mesh.scale.set(this.size, this.size, 1)
  }
  update (options = {}) {
    let self = this
    let {
      // 颜色
      color = self.color,
      // 透明度
      opacity = self.opacity,
      // 尺寸
      size = self.size,
      // 纹理
      map = self.map
    } = options
    this.color = color
    this.opacity = opacity
    this.size = size
    this.map = map
    this.mesh.material.color = new THREE.Color(this.color)
    this.mesh.material.opacity = this.opacity
    this.mesh.material.map = this.map
    this.mesh.material.map.needsUpdate = true
    this.mesh.scale.set(this.size, this.size, 1)
  }
  show () {
    this.mesh.visible = true
  }
  hide () {
    this.mesh.visible = false
  }
}



