 *     Modified By: qlq
 */

<template>
  <div>
    <svg :width="width" :height="height" @mouseout="onOut()">
      <g v-for="(item, index) in leaf" :transform="gTransform(item)" @mousemove="onMove($event, item, index)" :key="index" >
        <rect :width="leafWidth(item)" :height="leafHeight(item)" :style="leafStyle(item, index)" ></rect>
        <text :x="3" :y="leafTextY(item, index)" :style = 'leafTextStyle()'>{{item.data.name}}</text>
        <!-- <title>{{leafTitle(item)}}</title> -->
      </g>
    </svg>
    <svt-tooltip :data="mousePosition.value" :x="mousePosition.x" :y="mousePosition.y" v-show='mouse'/>
  </div>
</template>
<script>
import svtTooltip from "./../svt-common/svt-tooltip";
import * as d3 from "d3";
import TWEEN from "tween.js";
import { SIGVTALRM } from 'constants';
// import $ from 'jquery'
export default {
  name: "",
  data() {
    return {
      leaf:[],
      mouse:true,
      mousePosition:{
        x:0,
        y:0,
        value:''
      }
    };
  },
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    options: {
      type: Object,
      default: function() {
        return {};
      }
    },
    data: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  watch: {
    data: {
      handler() {
        if (this.data.name !== undefined) {
          this.dealData(this.data)
        }
      },
      deep: true
    },
    options: {
      handler() {
        this.dealData(this.data)
      },
      deep: true
    }
  },
  computed: {
    treeMap() {
      return d3
        .treemap()
        .size([this.width, this.height])
        .padding(this.options.padding)
        .round(true);
    },
  },
  methods: {
    onMove (event, item, index) {
      this.mouse = true
      let pLeft = event.currentTarget.parentElement.parentElement.getBoundingClientRect().left
      let pTop = event.currentTarget.parentElement.parentElement.getBoundingClientRect().top
      this.mousePosition.x = event.clientX - pLeft + 20
      this.mousePosition.y = event.clientY - pTop + 50
      this.mousePosition.value = `
              <div>
                <span style="font-size: 0">
                  <span style="
                      display: inline-block;
                      width: 12px;
                      height: 12px;
                      border-radius: 12px;
                      vertical-align: middle;
                      background-color: ${this.options.color[index]};"></span>
                </span>
                <span>${item.data.name + '：' + item.data.size + ' ' + this.options.tooltip.unit}</span>
              </div>
            `
    },
    onOut () {
      this.mouse = false
    },
    dealData (data) {
      let d = d3.hierarchy(data)
                .sum(d => d.size)
                .sort((a, b) => b.height - a.height || b.value - a.value)
      this.leaf = this.treeMap(d).leaves()
    },
    // 组的平移
     gTransform (item) {
       return `translate(${item.x0},${item.y0})`
    },
    leafWidth (item) {
      return item.x1 - item.x0
    },
    leafHeight (item) {
      return item.y1 - item.y0
    },
    leafStyle (item, index) {
      return {
        fill: this.options.color[index]
      }
    },
    leafTextY (item, index) {
      return this.options.padding + 3 + this.options.text.fontSize / 2
    },
    leafTitle (item) {
      return item.data.name + ':' + item.data.size
    },
    leafTextStyle () {
      return {
        'font-size': this.options.text.fontSize ? this.options.text.fontSize : 14,
        'font-family': this.options.text.fontFamily ? this.options.text.fontFamily : '微软雅黑',
        'font-weight': this.options.text.fontWeight ? this.options.text.fontWeight : 400,
        'color': this.options.text.color ? this.options.text.color : '#fff'

      }
    }
  },
  mounted() {
    this.dealData(this.data)
  },
  components: {
    svtTooltip
  }
};
</script>
<style scoped>
.tooltip-line {
  width: 1px;
  background: red;
  position: absolute;
}
</style>



