<!--
例子：
  <tooltip :val="val" :pStyle="pStyle">
    {{val}}
  </tooltip>
  val："悬浮显示内容"
  pStyle: {
    文字渐变之类的样式
  }
-->
<template>
  <div class = 'tooltip'>
    <div @mousemove = 'showCon' @mouseout = 'backCon' class = 'showCon' :style = "pStyle">
      <slot></slot>
    </div>
    <div class = 'con' v-show = 'show' :style = "{left: x + 'px', top: y + 'px', width: width + 'px'}">{{val}}</div>
  </div>
</template>
<script>
/* eslint-disable */
export default {
  name: 'tooltip',
  props: {
    val: {
      type: [String, Number],
      default: '无数据'
    },
    pStyle: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      title: '',
      show: false,
      x: 0,
      y: 0,
      width: '',
      height: ''
    }
  },
  methods: {
    showCon (event) {
      let width = this.val.length * 13
      let x = event.pageX
      this.x = event.pageX + 10
      this.y = event.pageY + 10
      if (width > (1920-x)) {
        this.width = (1920-x)*0.95
        let row = Math.ceil( ( (1920-x) * 0.8) / 13 )
        let col = Math.ceil( this.val.length / row )
        this.height = 13*col
      }
      this.show = true
    },
    backCon () {
      this.show = false
    }
  }
}
</script>

<style>
.tooltip{
  width: 100%;
  height: 100%;
}
.showCon{
  display: inline-block;
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.con{
  padding: 5px 10px;
  font-size: 15px;
  line-height: 20px;
  position: fixed;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, .4);
  color: white;
  white-space: normal;
  z-index: 50;
}
</style>



