       攻击者
    </div>
    <div class = "sz-module-center">
      <div class="cloumn">
        <div class="cloumntitle">攻击者所属国家排行</div>
        <svt-column :width="cwidth" :height="cheight" :options="coptions" :data="cdatas"/>
      </div>
      <div class="rank">
        <ul class="mark">
          <li v-for="(item,index) in marks" :key="index">{{ item }}</li>
        </ul>
        <svt-rank :data="datas" :options="options" :width="width" :height="height"></svt-rank>
      </div>
      <svg>
        <defs>
          <linearGradient id="reactfill1" x1="100%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:#ffcd0c;stop-opacity:1" />
            <stop offset="100%" style="stop-color:#ff9a00;stop-opacity:1" />
          </linearGradient>
        </defs>
        <defs>
          <linearGradient id="reactfill2" x1="100%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:#abf2ff;stop-opacity:1" />
            <stop offset="100%" style="stop-color:#8bceda;stop-opacity:1" />
          </linearGradient>
        </defs>
        <defs>
         <linearGradient id="reactfill3" x1="0%" y1="100%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:#ff9a00;stop-opacity:1" />
            <stop offset="100%" style="stop-color:#ffcd0c;stop-opacity:1" />
          </linearGradient>
        </defs>
        <defs>
          <linearGradient id="reactfill4" x1="0%" y1="100%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:#8bceda;stop-opacity:1" />
            <stop offset="100%" style="stop-color:#abf2ff;stop-opacity:1" />
          </linearGradient>
        </defs>
      </svg>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery'
import { index } from "@/api";
import svtRank from "@/components/charts/svt-rank/svt-rank";
import svtColumn from "@/components/charts/svt-column/svt-column";
export default {
  name: "aggressor",
  components: {
    svtRank,
    svtColumn
  },
  props: {},
  data() {
    return {
      // 组件所有配置项
      options: {
        // 标志位
        mark: "resource-item",
        // 是否需要排序
        sort: true,
        // {HTMLDom} 执行父元素element
        element: "",
        // 次数单位
        numUnit: {
          isShow: true,
          unitName: "次",
          topDis: 54.7,
          leftDis: 208,
          step: 39,
          fontSize: 12,
          color: "#ffffff",
          fontFamily: "FZLTHJW",
          difUnit: {
            isShow: true,
            unitName: ["TB", "GB", "MBPS"]
          }
          //    textAnchor: 'start'
        },
        // 类别名称
        categoryName: {
          // true显示，false不显示
          isShow: true,
          name: "攻击者参与事件数量排行",
          fontSize: 12,
          textNumWidth: 216,
          textNameHeight: 40,
          color: "#6ccdff",
          leftDis: -40,
          topDis: 0,
          fontFamily: "FZLTHJW"
        },
        // 模块中文字的设置
        text: {
          // 模块中条形图的名称字符
          textName: {
            // 是否显示悬浮窗
            lab: {
              isShow: false
            },
            // 设置长度
            textNameWidth: 90,
            // 设置高度
            textNameHeight: 40,
            // 背景颜色
            bg_color: "rgba(33,117,255,0.1)",
            lineHeight: 40,
            // 一列数据情况
            // 字符与左侧的边距
            leftDis: 4,
            // 字符与上方的边距
            topDis: 30,
            // 字符横向间距
            leftStep: 40,
            // 字符纵向间距
            topStep: 39,
            // 字符的大小
            fontSize: 12,
            // 字符的颜色
            color: "#d4edff",
            otherColor: {
              // 是否需要
              isShow: false,
              // 不同填充色情况
              color: [
                "#ff3c00",
                "#ffc000",
                "#57d2fb",
                "#08d1af",
                "#f66b68",
                "#5769fb",
                "#a361d6",
                "#8da4ff",
                "#37c43b",
                "#55acee"
              ],
              // 不同填充色的条数
              otherColorNum: 3
            },
            // 对齐方式  start middle end
            textAnchor: "middle",
            textAlign: "center",
            // 显示的字符串长度
            textLength: 100,
            // 两列数据情况
            // 字符与左侧的边距
            leftDis2: 22,
            // 字符与上方的边距
            topDis2: 45,
            // 字体
            fontFamily: "FZLTHJW"
          },
          // 模块中条形图显示的数量字符
          textNum: {
            // 是否显示悬浮窗
            lab: {
              isShow: true
            },
            // 设置长度
            textNumWidth: 30,
            // 设置字符对齐方式
            textAlign: "end",
            // 一列数据情况
            // 字符与左侧的边距
            leftDis: 178,
            // 字符与上方的边距
            topDis: 43,
            // 位置是否统一情况
            same: true,
            // 字符横向间距
            leftStep: 0,
            // 字符纵向间距
            topStep: 39.2,
            // 字符的大小
            fontSize: 12,
            // 字符的颜色
            color: "#ffffff",
            otherColor: {
              // 是否需要
              isShow: false,
              // 不同填充色情况
              color: [
                "#ff3c00",
                "#ffc000",
                "#57d2fb",
                "#08d1af",
                "#f66b68",
                "#5769fb",
                "#a361d6",
                "#8da4ff",
                "#37c43b",
                "#55acee"
              ],
              // 不同填充色的条数
              otherColorNum: 3
            },
            // 对齐方式  start middle end
            textAnchor: "end",
            // 显示的字符串长度
            textLength: 20,
            // 两列数据情况
            // 字符与左侧的边距
            leftDis2: 455,
            // 字符与上方的边距
            topDis2: 59,
            // 字体
            fontFamily: "FZLTHJW"
          }
        },
        // 模块中条形图的设置
        bar: {
          // 条形图距离模块顶部的距离
          topDis: 47,
          // 条形图距离模块左边的距离
          leftDis: 15,
          // 位置是否统一情况
          same: true,
          // 条形图之间的间隔
          step: 39,
          // 条形图的高度
          barHeight: 8,
          // 条形图的宽度
          barWidth: 154,
          // 圆角矩形rx值
          barRx: 0,
          // 圆角矩形ry值
          barRy: 0,
          // 填充颜色
          color: "url(#reactfill3)",
          otherColor: {
            // 是否需要
            isShow: true,
            // 不同填充色情况
            color: [
              "url(#reactfill3)",
              "url(#reactfill3)",
              "url(#reactfill3)",
              "url(#reactfill4)",
              "url(#reactfill4)"
            ],
            // 不同填充色的条数
            otherColorNum: 3
          }
        },
        // 条形图背景图设置
        bar_bg: {
          // 填充颜色
          color: "rgba(57,87,111,0.4)"
        },
        // 条形图按照百分比大小绘制
        percentName: {
          isShow: false
        },
        // 分段条形图
        section_bar: {
          // 是否显示
          isShow: false,
          // 每段宽度
          section_bar_width: 8,
          // 偏移量
          x: 130,
          y: 30,
          // 小矩形数量
          section_bar_num: 32,
          // 间隔大小
          section_bar_space: 1,
          step: 0
        },
        // 分段条形图背景图
        section_bar_bg: {
          // 是否显示
          isShow: true,
          // 每段宽度
          section_bar_bg_width: 8,
          // 间隔大小
          section_bar_bg_space: 1
        },
        // 数据列数
        columnNum: 1,
        data: []
      },
      // 组件显示高度
      height: 213,
      // 组件显示宽度
      width: 225,
      // 组件应用数据
      datas: [],
      // 展示页标题
      name: "排行图",
      // input行统一样式，必须写
      labelPosition: "right",
      // 是否显示loading（目前不好使，再调整）
      isLoad: true,
      marks: [],
      // 柱图-------------------------------------
      // 组件区域宽度
      cwidth: 452,
      // 组件区域高度
      cheight: 205,
      coptions: {
        className: "column2",
        marginTop: 25,
        marginBottom: 25,
        marginLeft: 50,
        marginRight: 25,
        // 悬浮提示的颜色
        tipcolor: [
          "#ff9900",
          "#ff9900",
          "#ff9900",
          "#6ccdff",
          "#6ccdff",
          "#6ccdff",
          "#6ccdff"
        ],
        // 柱图类型 normal, stack, moreCols
        type: "normal",
        // 柱形样式配置
        column: {
          // 单列宽度
          wid: 30,
          // 颜色
          color: [
            ["url(#reactfill1)"],
            ["url(#reactfill1)"],
            ["url(#reactfill1)"],
            ["url(#reactfill2)"],
            ["url(#reactfill2)"],
            ["url(#reactfill2)"],
            ["url(#reactfill2)"]
          ],
          // 小矩形块高度
          stackHig: 9,
          // 小矩形块间隔
          margin: 1,
          // 多列矩形间隔
          colMargin: 2,
          // 背景颜色
          bgcolor: "#39576f",
          //  补充到全高背景设置
          supplement: {
            show: true,
            color: "rgba(57,87,111,0.4)"
          }
        },
        axise: {
          // 坐标轴颜色
          color: "#495d78",
          // 坐标轴文字样式
          text: {
            fontFamily: "FZLTHJW",
            fontSize: 12,
            color: "#d4edff"
          },
          // 横轴配置项
          xAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: true,
            // 刻度长线是否展示
            tickLine: {
              show: false,
              // 线形：dotted虚线，solid实线
              linear: "dotted"
            },
            ticks: 6,
            // 自定义横轴显示的信息
            // 注意：使用了横轴显示信息后，center数组的数量应该与数据的条数一样多
            // 不需要显示的使用空字符串
            formate: {
              show: false,
              center: []
            }
          },
          yAxis: {
            // 坐标轴上线刻度是否展示
            tickShow: false,
            // 坐标轴上线是否展示
            lineShow: false,
            // 刻度长线是否展示
            tickLine: {
              show: true,
              // 线形：dotted虚线，solid实线
              linear: "dotted"
            },
            // 设置纵轴显示的刻度数
            ticks: 4
          }
        },
        tooltip: {
          unit: '次'
        }
      },
      cdatas: [],
      // 展示页标题
      cname: "柱图",
      // 坐标轴设置开关
      axisShow: false,
      // 柱图样式z设置开关
      columnShow: false
    };
  },
  // 计算变量
  computed: {
    // 获得格式化组件数据内容
    preOptions() {
      return JSON.stringify(this.options, null, 2);
    },
    // 获得格式化组件配置内容
    preData() {
      return JSON.stringify(this.data, null, 2);
    },
    // 全局时间轴选中的时间
    time() {
      return this.$store.state.time;
    }
  },
  // 方法
  methods: {
    // 获取攻击事件数量排行数据
    getEvent(params) {
      let promise = index.getAggressorEvent(params);
      promise
        .then(res => {
          if (res.data.data.length > 0) {
            let data = res.data.data;
            for (let i = 0; i < data.length; i++) {
              this.marks.push(i + 1);
            }
            // 根据每项value排降序
            for (var i = 0; i < data.length - 1; i++) {
              for (var j = data.length - 1; j > i; j--) {
                if (data[j].num > data[j - 1].num) {
                  var temp = data[j - 1];
                  data[j - 1] = data[j];
                  data[j] = temp;
                }
              }
            }
            data.forEach((d, i) => {
              let obj = {};
              obj.name = d.name;
              obj.num = d.num;
              obj.percent = d.percent;
              this.$set(this.datas, i, obj);
            });
          }
        })
        .catch(err => console.error(err));
    },
    // 获取攻击者所属国家排行数据
    getCountry(params) {
      let promise2 = index.getAggressorCountry(params);
      promise2
        .then(res => {
          if (res.data.data.length > 0) {
            let data = res.data.data;
            let transdata = [];
            for (let i = 0; i < data.length; i++) {
              transdata.push({
                org_name: data[i].name,
                children: [{ type: data[i].name, num: data[i].value }]
              });
            }
            transdata.forEach((d, i) => {
              let obj = {};
              obj.base = d.org_name;
              obj.a = d.children;
              this.$set(this.cdatas, i, obj);
            });
          }
        })
        .catch(err => console.error(err));
    }
  },
  // 监听
  watch: {
    cdatas: {
      handler() {
        if (this.datas !== 0) {
        }
      },
      deep: true
    },
    time(val, oldval) {
      let params = { time: this.time };
      this.getEvent(params);
      this.getCountry(params);
      this.marks = [];
    }
  },
  // 更新之后执行
  updated() {
    this.isLoad = false;
  },
  mounted() {
    let params = { time: this.time };
    this.getEvent(params);
    this.getCountry(params);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.aggressor {
  width: 754px;
  height: 278px;
  top: 350px;
  left: 1143px;
  padding: 4px 4px;
}
.rank {
  width: 216px;
  height: 213px;
  /* border: 1px solid red; */
  position: absolute;
  top: 50px;
  left: 522px;
}
.mark {
  list-style-type: none;
  /* border: 1px solid red; */
  position: absolute;
  top: 28px;
  left: 0;
  padding: 0;
}
.mark > li {
  font-family: FZLTHJW;
  font-size: 12px;
  color: #abf1fe;
  text-align: left;
  margin-bottom: 25px;
}
.mark > li:nth-child(-n + 3) {
  color: #ff9900;
}
.mark > li:last-child {
  margin-bottom: 0;
}
.svt-rank > div {
  text-align: left;
}
.cloumn {
  width: 452px;
  height: 213px;
  /* border: 1px solid red; */
  position: absolute;
  top: 50px;
  left: 22px;
}
.cloumntitle {
  font-size: 12px;
  color: #6ccdff;
  font-family: FZLTHJW;
  text-align: left;
}
.cloumn #svt-charts-column > g:nth-child(3) .svt-cloumn-groups rect {
  fill: url(#reactfill2);
}
</style>



