 *    @summary: 三维地图模块开发
 *    @version:v1.0.0
 *    Last Modified: 2018-10-31 15:16:51
 *    @Modified By:Gy
 */

<template>
  <div class="situationMapT" :style='{"width":tWidth + "px", "height": tHeight + "px"}'>
    <div class="webglContainer" ref="webglContainer"></div>
    <div class="controlContainer" ref="controlContainer"></div>
  </div>
</template>

<script>
import OrbitControls from 'three-orbitcontrols'
import MapBlockA from '@/components/charts/svt-mapT/map-block-a'
import MapLineA from '@/components/charts/svt-mapT/map-line-a'
import EffectMapPointA from '@/components/charts/svt-mapT/effect-map-point-a'
import EffectMapCurveA from '@/components/charts/svt-mapT/effect-map-curve-a'
import EffectMapCurveParticleA from '@/components/charts/svt-mapT/effect-map-curve-particle-a'
import {coordTransform} from '@/components/charts/svt-mapT/common/coordTransform'
import {index} from '@/api'
// 加载数据
import worldcountiresData from '@/assets/data/map-common/worldcountries.json'
let THREE = window.THREE
let jQuery = window.jQuery
let TWEEN = window.TWEEN
export default {
  name: 'situationMap',
  props: {
    tWidth: {
      type: Number,
      default: 1102
    },
    tHeight: {
      type: Number,
      default: 639
    },
    tShow: {
      'event': {
        '拒绝服务攻击': true,
        '勒索': true,
        '威胁网络流量': true,
        '蠕虫': true,
        '远控': true,
        '漏洞利用': true,
        '后门': true,
        '高级持续威胁': true
      }
    }
  },
  data () {
    return {
      // 动画序列号
      requestId: null,
      // 纹理列表
      textureLibs: {
        'shadowMap': new THREE.TextureLoader().load(require('@/assets/images/mapT/shadowmap.png')),
        'asset': new THREE.TextureLoader().load(require('@/assets/images/mapT/asset.png')),
        'attack': new THREE.TextureLoader().load(require('@/assets/images/mapT/attack.png'))
      },
      // 安全事件
      safeEvent: [],
      // 安全事件展示路径对象池数组
      pathCurvesList: [],
      // 安全事件展示路径可视对象池数组
      visPathCurvesList: [],
      // 威胁情报
      safeInfo: [],
      // 威胁情报展示节点对象池数组
      pointsList: [],
      // 威胁情报展示节点可视对象池数组
      visPointsList: []
    }
  },
  computed: {
    // 全局时间轴选中的时间
    time () {
      return this.$store.state.time
    }
  },
  watch: {
    safeEvent: function (val) {
      this.recycling()
      for (let ts in this.tShow) {
        switch (ts) {
          case 'event':
            this.updateEffectB(this.safeEvent)
            break
          case 'info':
            this.updateEffectA(this.safeInfo)
            break
        }
      }
    },
    safeInfo: function (val) {
      this.recycling()
      for (let ts in this.tShow) {
        switch (ts) {
          case 'event':
            this.updateEffectB(this.safeEvent)
            break
          case 'info':
            this.updateEffectA(this.safeInfo)
            break
        }
      }
    },
    tShow: function (val) {
      console.log(val)
      this.recycling()
      for (let ts in this.tShow) {
        switch (ts) {
          case 'event':
            this.updateEffectB(this.safeEvent)
            break
          case 'info':
            this.updateEffectA(this.safeInfo)
            break
        }
      }
    },
    time (val, oldval) {
      let params = {time: this.time}
      this.getInformationMap(params)
      this.getEventMap(params)
    }
  },
  methods: {
    renderGL: function () {
      TWEEN.update()
      this.renderer.render(this.scene, this.camera)
      this.requestId = requestAnimationFrame(this.renderGL)
    },
    computeWidth: function () {
      let width = jQuery('.situationMapT')[0].clientWidth
      return width
    },
    computeHeight: function () {
      let height = jQuery('.situationMapT')[0].clientHeight
      return height
    },
    handleWindowResize: function () {
      let width = this.computeWidth()
      let height = this.computeHeight()
      this.renderer.setClearColor(0xffffff * Math.random())
      this.renderer.setSize(width, height)
      if (this.camera.type === 'OrthographicCamera') {
        this.camera.top = this.camera.viewWidth / 2 * height / width
        this.camera.bottom = -this.camera.viewWidth / 2 * height / width
      } else {
        this.camera.aspect = width / height
      }
      this.camera.updateProjectionMatrix()
    },
    // 事件回收
    recycling: function () {
      TWEEN.removeAll()
      while (this.visPathCurvesList.length > 0) {
        let visCurve = this.visPathCurvesList.shift()
        visCurve.hide()
        visCurve.point.hide()
        visCurve.pathCurvePart.hide()
        this.pathCurvesList.push(visCurve)
      }
      while (this.visPointsList.length > 0) {
        let visPoint = this.visPointsList.shift()
        visPoint.hide()
        this.pointsList.push(visPoint)
      }
    },
    updateEffectA: function (val) {
      // 威胁情报
      let map, color
      for (let i = 0, il = val.length; i < il; i++) {
        if (!this.tShow.info[val[i].type]) continue
        switch (val[i].sort) {
          case 'attack':
            map = this.textureLibs.attack
            switch (val[i].type) {
              case 'C&C':
                color = '#fc76c8'
                break
              case '扫描IP':
                color = '#62d2fd'
                break
              case '漏洞攻击IP':
                color = '#61ffc5'
                break
              default:
                color = '#ffffff'
            }
            break
          case 'asset':
            map = this.textureLibs.asset
            switch (val[i].type) {
              case '肉鸡':
                color = '#fc605c'
                break
              case '受扫描攻击资产':
                color = '#ff995c'
                break
              case '受漏洞攻击资产':
                color = '#fedd6d'
                break
              default:
                color = '#ffffff'
            }
            break
        }
        let point, coord
        if (this.pointsList.length > 0) {
          point = this.pointsList.shift()
          point.update({color: color, map: map, size: 1})
        } else {
          point = new EffectMapPointA({map: map, size: 1, color: color})
        }
        coord = coordTransform({coord: val[i].coord, isMercator: true})
        point.mesh.position.set(coord[0], coord[1], 1)
        this.worldContainer.add(point.mesh)
        this.visPointsList.push(point)
        new TWEEN.Tween({t: 1})
          .to({t: 10}, 1000)
          .yoyo(1)
          .delay(5000 * Math.random())
          .easing(TWEEN.Easing.Linear.None)
          .repeat(Infinity)
          .onStart(function () {
            point.show()
          })
          .onUpdate(function (time) {
            point.mesh.scale.set(time.t, time.t, 1)
          })
          .start()
      }
    },
    updateEffectB: function (val) {
      // 威胁事件
      let color
      for (let i = 0, il = val.length; i < il; i++) {
        if (!this.tShow.event[val[i].type]) continue
        switch (val[i].type) {
          case '拒绝服务攻击':
            color = '#CB3221'
            break
          case '勒索':
            color = '#FEFE1B'
            break
          case '远控':
            color = '#FF9C1B'
            break
          case '漏洞利用':
            color = '#95FE1B'
            break
          case '后门':
            color = '#6acdff'
            break
          case '威胁网络流量':
            color = '#62ffdc'
            break
          case '蠕虫':
            color = '#8635FE'
            break
          case '高级持续威胁':
            color = '#FF73F3'
            break
          default:
            color = '#ffffff'
        }
        let point, coord, pathCurve, pathCurvePart
        if (this.pathCurvesList.length > 0) {
          pathCurve = this.pathCurvesList.shift()
          point = pathCurve.point
          pathCurvePart = pathCurve.pathCurvePart
          pathCurve.update({
            color: color,
            source: val[i].src,
            target: val[i].dst
          })
          point.update({
            color: color
          })
          pathCurvePart.update({
            color: color,
            source: val[i].src,
            target: val[i].dst})
        } else {
          point = new EffectMapPointA({map: this.textureLibs.asset, size: 1, color: color})
          pathCurve = new EffectMapCurveA({
            direction: false,
            isMercator: true,
            color: color,
            source: val[i].src,
            target: val[i].dst,
            opacity: 0.2
          })
          pathCurvePart = new EffectMapCurveParticleA({
            direction: false,
            isMercator: true,
            pointsCount: 300,
            sizeMax: 5.0,
            expansion: 2,
            pointsVisibleCount: 100,
            color: color,
            source: val[i].src,
            target: val[i].dst
          })
          pathCurve.point = point
          pathCurve.pathCurvePart = pathCurvePart
          this.worldContainer.add(point.mesh)
          this.worldContainer.add(pathCurvePart.mesh)
          this.worldContainer.add(pathCurve.mesh)
        }
        coord = coordTransform({coord: val[i].dst, isMercator: true})
        point.mesh.position.set(coord[0], coord[1], 1)
        point.hide()
        pathCurvePart.hide()
        pathCurve.hide()
        this.visPathCurvesList.push(pathCurve)
        this.singleLineAni(pathCurve, val[i])
      }
    },
    singleLineAni: function (obj) {
      let that = this
      new TWEEN.Tween({t: 0})
        .to({t: 1}, 1000)
        .onStart(function () {
          obj.show()
        })
        .easing(TWEEN.Easing.Linear.None)
        .delay(Math.random() * 10000)
        .onUpdate(function (d) {
          obj.animate({delta: d.t})
        })
        .onComplete(function () {
          new TWEEN.Tween({tt: 0})
            .to({tt: 1}, 5000)
            .onStart(function () {
              obj.pathCurvePart.show()
            })
            .easing(TWEEN.Easing.Linear.None)
            .onUpdate(function (dd) {
              obj.pathCurvePart.animate({delta: dd.tt})
            })
            .onComplete(function () {
              that.singleLineAni(obj)
              obj.hide()
              obj.pathCurvePart.hide()
              // obj.point.hide()
            })
            .start()
          // new TWEEN.Tween({ttt: 1})
          //   .to({ttt: 10}, 500)
          //   .onStart(function () {
          //     obj.point.show()
          //   })
          //   .easing(TWEEN.Easing.Linear.None)
          //   .onUpdate(function (ddd) {
          //     obj.point.mesh.scale.set(ddd.ttt, ddd.ttt, 1)
          //   })
          //   .start()
        })
        .start()
    },
    getEventMap (params) {
      let promiseEvent = index.getSafeEventMap(params)
      promiseEvent.then((res) => {
        if (res.data.status === 'success') {
          this.safeEvent = res.data.data
        }
      })
    },
    getInformationMap (params) {
      // 威胁情报
      let promiseInfo = index.getSafeInformationMap(params)
      promiseInfo.then((res) => {
        if (res.data.status === 'success') {
          this.safeInfo = res.data.data
        }
      })
    }
  },
  created () {
    // 场景
    this.scene = new THREE.Scene()
    // 渲染器
    this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true})
    this.renderer.setClearColor(0x000000, 0.0)
    // 世界地图容器
    this.worldContainer = new THREE.Object3D()
    this.worldContainer.position.set(220, 0, 0)
    this.scene.add(this.worldContainer)
    // 世界地图
    this.worldMapBlock = new MapBlockA({color: '#17629C', isChinaCenter: true, isMercator: true})
    this.worldMapLine = new MapLineA({color: '#144B78', isChinaCenter: true, isMercator: true, offsetY: -15})
    // 阴影板
    let shadowBlock = new THREE.Mesh(
      new THREE.PlaneBufferGeometry(430, 240, 1, 1),
      new THREE.MeshBasicMaterial({
        color: 0xffffff,
        transparent: true,
        map: this.textureLibs.shadowMap
      })
    )
    shadowBlock.position.z = -1
    shadowBlock.position.x = -220
    this.worldContainer.add(shadowBlock)
  },
  mounted () {
    let width = this.computeWidth()
    let height = this.computeHeight()
    this.renderer.setSize(width, height)
    this.$refs.webglContainer.appendChild(this.renderer.domElement)
    // 摄像机 正交摄像机
    let viewWidth = 450
    this.camera = new THREE.OrthographicCamera(-viewWidth / 2, viewWidth / 2, viewWidth / 2 * height / width, -viewWidth / 2 * height / width, -1000, 1000)
    this.camera.viewWidth = viewWidth
    this.camera.position.set(0, 0, 1)
    this.camera.view = {enabled: true, fullWidth: 1, fullHeight: 1, offsetX: 0, offsetY: 0, width: 1, height: 1}
    this.camera.updateProjectionMatrix()
    // 控制器
    this.control = new OrbitControls(this.camera, this.$refs.controlContainer)
    this.control.screenSpacePanning = true
    this.control.enablePan = true
    this.control.enableRotate = false
    this.control.enableZoom = true
    // 添加地图效果
    this.worldMapBlock.update(worldcountiresData.features)
    this.worldMapBlock.setAreaColor({'areaId': 'China', color: '#2E93E5'})
    this.worldContainer.add(this.worldMapBlock.mesh)
    this.worldMapLine.update(worldcountiresData.features)
    this.worldContainer.add(this.worldMapLine.mesh)
    // 加载数据
    // for (let ts in this.tShow) {
    //   switch (ts) {
    //     case 'event':
    //       this.updateEffectB(this.safeEvent)
    //       break
    //     case 'info':
    //       this.updateEffectA(this.safeInfo)
    //       break
    //   }
    // }
    // 威胁事件
    let params = {time: this.time}
    this.getInformationMap(params)
    this.getEventMap(params)

    // 执行循环渲染动画
    this.renderGL()
  },
  beforeDestroy () {
    this.renderer.forceContextLoss()
    cancelAnimationFrame(this.requestId)
  }
}
</script>

<style scoped>
  .situationMapT {
    position: relative;
    margin: 0px;
    padding: 0px;
    top: 0px;
  }
  .webglContainer {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
  }
  .controlContainer {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
  }
</style>



