
/*
 *     Created on 2018-07-10 14:46:23
 *     @author: LJG
 *     @summary: svtcharts-泳道图组件-事件控制
 *     @version: v1.0.0
 *     Last Modified: 2018-07-10 14:46:23
 *     Modified By: liujianguo
 */
<template>
  <div
    @mouseup="_handleMouseUp"
    @mousedown="_handleMouseDown"
    @mousemove="_handleMouseMove"
    @mouseleave="_handleMouseLeave"
    @mousewheel.prevent="_handleMouseWheel"
    >
  </div>
</template>

<script>
export default {
  data () {
    return {
      anchor: null,
      current: null,
      dragMode: false
    }
  },
  methods: {
    _handleMouseDown (event) {
      if (event.button !== 0) return
      event.stopPropagation()
      this.dragMode = true
      const {left, top} = this.$el.getBoundingClientRect()
      this.anchor = {
        x: event.clientX - left,
        y: event.clientY - top
      }
    },
    _handleMouseUp (event) {
      if (!this.anchor) return
      event.stopPropagation()
      const {anchor, current} = this
      let end = null
      if (current) {
        end = current
      } else {
        end = anchor
      }
      const x = anchor.x < end.x ? anchor.x : end.x
      const y = anchor.y < end.y ? anchor.y : end.y
      this.anchor = null
      this.current = null
      this.$emit('select', x, y)
      if (this.dragMode) {
        this.dragMode = false
      }
    },
    _handleMouseLeave (event) {
      this._handleMouseUp(event)
    },
    _handleMouseMove (event) {
      const {left, top} = this.$el.getBoundingClientRect()
      event.stopPropagation()
      const prevCurrent = this.current || this.anchor
      this.current = {
        x: event.clientX - left,
        y: event.clientY - top
      }
      if (this.dragMode) {
        if (!this.anchor) return
        this.$emit('drag', this.current.x - prevCurrent.x)
      } else {
        this.$emit('move', this.current.x, this.current.y)
      }
    },
    _handleMouseWheel (event) {
      event.stopPropagation()
      const {left} = this.$el.getBoundingClientRect()
      this.$emit('scroll', event.clientX - left, event.deltaY)
    }
  }
}
</script>



