/*
 *     Created on 2018-07-09 17:48:53
 *     @author: QLQ
 *     @summary:折线图组件
 *     @version: v1.0.0
 *     Last Modified: '2018-07-09 17:56:06
 *     Modified By: qlq
 */

<template>
  <div>
    <svg :width="width" :height="height" ref="svgRoot">
      <g id='svt-line'>
        <svt-axis orient="axisBottom" :scale="scaleX" :options="axisOptionsX" :transform="xTranslate" v-if="isXaxis"  v-on:dTick="drawXTick"/>
        <svt-axis orient="axisLeft" :scale="scaleY" :options="axisOptionsY" :transform="yTranslate" v-on:dTick="drawYTick"/>
        <g  :transform="xFormateTranslate" v-show='options.axise.xAxis.formate.show'>
          <text v-for='(d,index) in options.axise.xAxis.formate.center' :key='index' :x="scaleX(index)" y=0 :style='xFormateStyle'>{{options.axise.xAxis.formate.center[index]}}</text>
        </g>
        <g v-for='(d,index) in data' :key=index :style='gStyle'>
          <path :d="lineFun(d)"  :stroke='options.line.stroke[index]' stroke-width=2 fill='none' v-show="options.line.show"></path>
          <path :d="areaFun(d)"  :fill='options.area.fill[index]'  v-show="options.area.show"></path>
         <g v-for='(c,i) in d' :key=i>
            <circle :r='options.circle.r' :class='caculateClass(i).circle' :cx='circleX(c.x)' :cy='scaleY(c.y)' stroke-width=2 :stroke='options.circle.stroke[index]' :fill='options.circle.fill[index]'  v-show='options.circle.show'/>
          </g>
        </g>
      </g>
    </svg>
    <svt-line-plane @move="onMove" :style="planeStyle" @leave="out" @scroll='onScroll'></svt-line-plane>
    <svt-tooltip :data="tooltipData" :x="mousePosition.x" :y="mousePosition.y" v-show='mouseout&&options.tooltip.show'/>
    <div class='tooltip-line' :style='tooltipLine' v-show='mouseout&&options.tooltip.show'></div>
  </div>
</template>
<script>
import svtAxis from '../svt-common/svt-axis'
import svtLinePlane from './svt-line-plane'
import svtTooltip from './../svt-common/svt-tooltip'
import * as d3 from 'd3'
import TWEEN from 'tween.js'
// import $ from 'jquery'
export default {
  name: '',
  data () {
    return {
      // 动画变化的值
      gTween: '',
      // 数值范围Y
      domainY: [100, 1],
      isXaxis: true,
      // 鼠标在事件容器内的位置
      mousePosition: {
        // 鼠标在容器内的横坐标
        x: 0,
        // 鼠标在容器内的纵坐标
        y: 0,
        // 获得提示信息线的位置
        x0: 0
      },
      // 鼠标是否在事件容器内
      mouseout: false,
      // 提示内容的存储对象
      tooltipData: '',
      // 页码总数
      pageAll: 0,
      // 数据总条数
      pageDataAll: 0,
      // 分页后单页的数据
      data: [],
      // 分页后单页的很轴格式数组
      xFormate: [],
      // 分页后单页的各横轴节点对应的纵轴数据
      yData: []
    }
  },
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    options: {
      type: Object,
      default: function () {
        return {
          width: 700,
          height: 400,
          margin: [25, 25, 25, 50],
          // curve曲线,normal正常线
          type: 'curve',
          line: {
            // 是否展示折线
            show: true,
            // 折线线的颜色
            stroke: ['#fff', 'red']
          },
          area: {
            show: true,
            fill: ['rgba(10,10,10,0.5)', 'rgba(10,10,10,0.5)']
          },
          circle: {
            show: true,
            stroke: ['#fff', '#fff'],
            fill: ['red', 'red'],
            r: 5
          },
          tooltip: {
            show: false,
            lineColor: 'blue'
          },
          axise: {
            // 坐标轴颜色
            color: 'red',
            // 坐标轴文字样式
            text: {
              fontFamily: '微软雅黑',
              fontSize: 14,
              color: 'yellow'
            },
            // 横轴配置项
            xAxis: {
              // 坐标轴上线刻度是否展示
              tickShow: true,
              // 坐标轴上线是否展示
              lineShow: true,
              // 刻度长线是否展示
              tickLine: {
                show: true,
                // 线形：dotted虚线，solid实线
                linear: 'dotted'
              },
              formate: {
                show: true,
                center: []
              }
            },
            yAxis: {
              // 坐标轴上线刻度是否展示
              tickShow: true,
              // 坐标轴上线是否展示
              lineShow: true,
              // 刻度长线是否展示
              tickLine: {
                show: true,
                // 线形：dotted虚线，solid实线
                linear: 'dotted'
              }
            }
          }
        }
      }
    },
    datas: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  watch: {
    data: {
      handler () {
        if (this.data !== 0) {
          this.isXaxis = true
          this.run()
        }
      },
      deep: true
    },
    options: {
      handler () {
        this.run()
      },
      deep: true
    }
  },
  computed: {
    // 生成样式
    gStyle () {
      return {
        transformOrigin: '50% 100%',
        // scaleY(0.5) translate(50px, 25px)
        transform: `${this.gTween}  translate(${this.options.margin[3]}px, ${
          this.options.margin[0]
        }px)`
      }
    },
    xFormateStyle () {
      const bottom = this.options.axise
      return {
        'fill': bottom.color,
        'font-size': bottom.text.fontSize,
        'font-family': bottom.text.fontSize,
        'text-anchor': 'middle'
      }
    },
    xFormateTranslate () {
      let translate = `translate(${this.options.margin[3]}, ${this.height - this.options.margin[2] + this.options.axise.text.fontSize + 10})`
      return translate
    },
    // 计算当前比例尺应用范围
    range () {
      return {
        x: [0, this.width - this.options.margin[1] - this.options.margin[3]],
        y: [this.height - this.options.margin[0] - this.options.margin[2], 0]
      }
    },
    // 设置x轴域值
    // 根据当前页码对应的起始条数
    domainX () {
      return [
        (this.options.pageNum - 1) * this.options.pageSize,
        this.options.pageNum * this.options.pageSize - 1 < this.pageDataAll
          ? this.options.pageNum * this.options.pageSize - 1
          : this.pageDataAll - 1
      ]
    },
    // 设置x轴比例尺
    scaleX () {
      const { range, domainX } = this
      return d3
        .scaleLinear()
        .range(range.x)
        .domain(domainX)
    },
    // 设置y轴比例尺
    scaleY () {
      this.ddata()
      let column = []
      for (let i = 0; i < this.data.length; i++) {
        for (let ii = 0; ii < this.data[i].length; ii++) {
          column.push(this.data[i][ii].y)
        }
      }
      const domainY = [0, Math.max(...column)]
      const { range } = this
      return d3
        .scaleLinear()
        .range(range.y)
        .domain(domainY)
    },
    // 传入配置参数X轴
    axisOptionsX () {
      const bottom = this.options.axise
      return {
        color: bottom.color,
        fontSize: bottom.text.fontSize,
        fontFamily: bottom.text.fontSize,
        tickFormat: this.axisFormat
      }
    },
    // 传入配置参数Y轴
    axisOptionsY () {
      const bottom = this.options.axise
      return {
        color: bottom.color,
        fontSize: bottom.text.fontSize,
        fontFamily: bottom.text.fontSize,
        tickFormat: '',
        tick: bottom.yAxis.ticks
      }
    },
    // 获取图表实际宽度
    widthColum () {
      return this.width - this.options.margin[1] - this.options.margin[3]
    },
    // 获取图表实际高度
    heightColum () {
      return this.height - this.options.margin[0] - this.options.margin[2]
    },
    // x的偏移
    xTranslate () {
      let translate = `translate(${this.options.margin[3]}, ${this.height -
        this.options.margin[2]})`
      return translate
    },
    // y的偏移
    yTranslate () {
      let translate = `translate(${this.options.margin[3]}, ${
        this.options.margin[0]
      })`
      return translate
    },
    // 事件层div样式返回函数
    planeStyle () {
      return {
        position: 'absolute',
        transform: `translate(${this.options.margin[3]}px,${
          this.options.margin[0]
        }px)`,
        width: this.widthColum + 'px',
        height: this.heightColum + 'px',
        background: 'tranparent',
        top: '0px',
        left: '0px'
      }
    },
    // 提示信息线位置返回函数
    tooltipLine () {
      return {
        height: this.heightColum + 'px',
        left: this.mousePosition.x0 + this.options.margin[3] + 'px',
        top: this.options.margin[0] + 'px',
        background: this.options.tooltip.lineColor
      }
    }
  },
  methods: {
    // 重绘y轴的先关配置项
    drawYTick (ele) {
      // 是否显示坐标轴上的线
      if (!this.options.axise.yAxis.lineShow) {
        ele.selectAll('path').style('display', 'none')
      }
      // 是否显示坐标轴上tick
      if (!this.options.axise.yAxis.tickShow) {
        ele.selectAll('line').style('display', 'none')
      }
      // 生成刻度长线
      let tickLine
      if (this.options.axise.yAxis.tickLine.show) {
        tickLine = ele
          .selectAll('.tick')
          .append('line')
          .attr('x1', 0)
          .attr('y1', 0)
          .attr('x2', this.widthColum)
          .attr('y2', 0)
      }
      if (this.options.axise.yAxis.tickLine.show && this.options.axise.yAxis.tickLine.linear === 'dotted') {
        tickLine.attr('stroke-dasharray', '5,5')
      }
      // 重置坐标轴文字颜色
      ele.selectAll('text').style('fill', this.options.axise.text.color ? this.options.axise.text.color : '#fff')
    },
    // 重新绘制x轴相关配置项
    drawXTick (ele) {
      // 是否显示坐标轴上的线
      if (!this.options.axise.xAxis.lineShow) {
        ele.selectAll('path').style('display', 'none')
      }
      // 是否显示坐标轴上tick
      if (!this.options.axise.xAxis.tickShow) {
        ele.selectAll('line').style('display', 'none')
      }
      // 生成刻度长线
      let tickLine
      if (this.options.axise.xAxis.tickLine.show) {
        tickLine = ele
          .selectAll('.tick')
          .append('line')
          .attr('x1', 0)
          .attr('y1', 0)
          .attr('x2', 0)
          .attr('y2', 0 - this.heightColum)
      }
      if (this.options.axise.xAxis.tickLine.show && this.options.axise.xAxis.tickLine.linear === 'dotted') {
        tickLine.attr('stroke-dasharray', '5,5')
      }
      // 重置坐标轴文字颜色
      ele.selectAll('text').style('fill', this.options.axise.text.color ? this.options.axise.text.color : '#fff')
    },
    ddata () {
      // 重置数据数组
      this.data = []
      this.xFormate = []
      this.yData = []
      const { datas } = this
      // 设置页面总页数
      this.pageAll =
        datas.length % this.options.pageSize > 0
          ? Math.floor(datas.length / this.options.pageSize) + 1
          : datas.length / this.options.pageSize
      // 设置页面总条数
      this.pageDataAll = datas.length
      // 计算开始位置、结束位置
      let first = (this.options.pageNum - 1) * this.options.pageSize
      let last =
        this.options.pageNum * this.options.pageSize - 1 >= this.pageDataAll
          ? this.pageDataAll - 1
          : this.options.pageNum * this.options.pageSize - 1
      // 根据开始位置和结束位置计算出数据data、横轴xFormate、每个横轴节点对应的纵轴数值yData
      for (let i = first; i <= last; i++) {
        this.xFormate.push(this.options.axise.xAxis.formate.show ? this.options.axise.xAxis.formate.center[i] : datas[i].name)
        this.yData.push(datas[i].value)
        for (let ii = 0; ii < datas[i]['value'].length; ii++) {
          if (i === first) {
            this.data.push([])
          }
          let ob = {}
          ob.x = i
          ob.y = datas[i].value[ii]
          this.data[ii].push(ob)
        }
      }
    },
    // 横轴模板函数
    axisFormat (i) {
      // 将域转换为数组
      let domain = []
      for (let i = this.domainX[0]; i <= this.domainX[1]; i++) {
        domain.push(i)
      }
      let ordinal = d3
        .scaleOrdinal()
        .range(this.xFormate)
        .domain(domain)
      if (!this.options.axise.xAxis.formate.show) {
        return i === Math.round(i) ? ordinal(i) : ''
      }
    },
    // 生成线生成器
    lineFun (_data, status) {
      const { scaleX, scaleY } = this
      let line = d3
        .line()
        .x(function (d) {
          return scaleX(d.x)
        })
        .y(function (d) {
          return status === 'init' ? scaleY(0) : scaleY(d.y)
        })
      if (this.options.type === 'curve') {
        line.curve(d3.curveCatmullRom.alpha(0.5))
      }
      return line(_data)
    },
    // 生成面积生成器
    areaFun (_data, status) {
      const { scaleX, scaleY } = this
      let area = d3
        .area()
        .x(function (d) {
          return scaleX(d.x)
        })
        .y1(function (d) {
          return status === 'init' ? scaleY(0) : scaleY(d.y)
        })
        .y0(function (d) {
          return scaleY(0)
        })
      if (this.options.type === 'curve') {
        area.curve(d3.curveCatmullRom.alpha(0.5))
      }
      return area(_data)
    },
    // 生成画圆节点x坐标
    circleX (x) {
      const { scaleX } = this
      return scaleX(x)
    },
    // 生成画圆节点y坐标
    circleY (y) {
      const { scaleY } = this
      return scaleY(y)
    },
    // 定义类名
    caculateClass (index) {
      return {
        line: `svt-line${index}`,
        area: `svt-area${index}`,
        circle: `svt-circle${index}`
      }
    },
    run () {
      let _self = this
      function animate () {
        if (TWEEN.update()) {
          requestAnimationFrame(animate)
        }
      }
      new TWEEN.Tween({ val: 0 })
        .to({ val: 1 }, 500)
        .onUpdate(function () {
          _self.gTween = `scaleY(${this.val})`
        })
        .start()
      animate()
    },
    onMove (x, y) {
      // 计算横轴有几个刻度间距
      // 最后一页要取剩余数据数减1
      let ticks =
        this.options.pageNum * this.options.pageSize < this.pageDataAll
          ? this.options.pageSize - 1
          : this.pageDataAll - (this.pageAll - 1) * this.options.pageSize - 1
      // 计算单个刻度间距的宽度，当只有一个刻度时刻度间距为width
      let w = ticks > 0 ? this.widthColum / ticks : this.widthColum
      // 计算当前位置属于哪个刻度
      let i = ticks > 0 ? parseInt(x % w <= w / 2 ? x / w : x / w + 1) : 0
      // 更新线的横坐标
      this.mousePosition.x0 = i * w
      // 定义提示框的横坐标
      this.mousePosition.x = x + 100
      // 定义提示框的纵坐标
      this.mousePosition.y = y + 20
      // 显示提示框时节点放大
      if (this.options.tooltip.show) {
        d3.selectAll('#svt-line circle').attr('r', this.options.circle.r)
        d3.selectAll(`.svt-circle${i}`).attr('r', this.options.circle.r + 2)
      }
      // 显示提示信息
      this.mouseout = true
      // 提示框组件添加数据
      this.tooltipData = ''
      for (let j = 0; j < this.yData[i].length; j++) {
        this.tooltipData += `
              <div>
                <span style="font-size: 0">
                  <span style="
                      display: inline-block;
                      width: 12px;
                      height: 12px;
                      border-radius: 12px;
                      vertical-align: middle;
                      background-color: ${this.options.line.stroke[j]};"></span>
                </span>
                <span>${this.xFormate[i] + '：' + this.yData[i][j] + ' ' +this.options.tooltip.unit}</span>
              </div>
            `
      }
    },
    out () {
      d3.selectAll('#svt-line circle').attr('r', this.options.circle.r)
      // 不显示提示信息
      this.mouseout = false
    },
    onScroll (x, delta) {
      if (delta > 0 && this.options.pageNum < this.pageAll) {
        this.options.pageNum++
      }
      if (delta < 0 && this.options.pageNum > 1) {
        this.options.pageNum--
      }
      // 不显示提示信息
      this.mouseout = false
      // 重新计算折线图数据
      this.ddata()
    }
  },
  mounted () {
    this.run()
  },
  components: {
    svtAxis,
    svtLinePlane,
    svtTooltip
  }
}
</script>
<style scoped>
.svt-line-num1 {
  fill: none;
}
.svt-area-num1 {
  stroke: none;
}
.tooltip-line {
  width: 1px;
  background: red;
  position: absolute;
}
</style>



