/*
 *      Created on 2018-07-09 13:24:43
 *      @author: ✖＋※
 *      @summary: svtcharts-排行图组件
 *      @version: v1.0.0
 *      Last Modified: 2018-07-10 11:14:08
 *      MOdified By: 程家兴
 */

<template>
  <div class='svt-rank'>
      <svg class="svt-rank-chart" :width="width" :height="height">
        <!--// 常规条形图的条形、背景、单位-->
        <g v-for="(d, index) in data" :key="bindKey(2, index)" v-if="!options.section_bar.isShow">
          <rect
          :width = "options.bar.barWidth"
          :height = "options.bar.barHeight"
          :fill = "options.bar_bg.color"
          :rx = "options.bar.barRx"
          :ry = "options.bar.barRy"
          :transform = "groups (index, options.bar.leftDis, options.bar.topDis, options.bar.step)"></rect>
          <text v-if="options.numUnit.isShow"
          :font-size = "options.numUnit.fontSize"
          :fill = "options.numUnit.color"
          :font-family = "options.numUnit.fontFamily"
          :transform = "groups (index, options.numUnit.leftDis, options.numUnit.topDis, options.numUnit.step)">
          {{ options.numUnit.unitName }}
          </text>
        </g>
         <g v-for="(d, index) in realWidth" :key="bindKey(1, index)" v-if="!options.section_bar.isShow">
          <rect
          :width = "d"
          :height = "options.bar.barHeight"
          :fill = "barColor(index)"
          :rx = "options.bar.barRx"
          :ry = "options.bar.barRy"
          :transform = "groups(index, options.bar.leftDis,options.bar.topDis, options.bar.step)"></rect>
        </g>
        <!--// 分段条形图的条形、背景、单位-->
        <g v-for="(d, index) in data" :key="bindKey(3, index)" v-if=" options.section_bar.isShow">
           <!--// 背景条形-->
          <rect v-for="(d3, index2) in sectionBarBgNum()" v-if="options.section_bar_bg.isShow"
          :key="bindKey(5, index2)"
          :width = "options.section_bar.section_bar_width"
          :height = "options.bar.barHeight"
          :fill = "options.bar_bg.color"
          :rx = "options.bar.barRx"
          :ry = "options.bar.barRy"
          :transform = "groups (index, options.bar.leftDis, options.bar.topDis, options.section_bar.step)"
          :x = " (options.section_bar_bg.section_bar_bg_width + options.section_bar_bg.section_bar_bg_space) * (index2)"
          :y = "options.bar.step * index + options.bar.topDis"></rect>
          <!--// 条形-->
          <rect v-for="(d, index2) in sectionBarNum(index)" :key="bindKey(4, index2)"
          :width = "options.section_bar.section_bar_width"
          :height = "options.bar.barHeight"
          :fill = "barColor(index)"
          :rx = "options.bar.barRx"
          :ry = "options.bar.barRy"
          :transform = "groups (index, options.bar.leftDis, options.bar.topDis, options.section_bar.step)"
          :x = " (options.section_bar.section_bar_width + options.section_bar.section_bar_space) * (index2)"
          :y = "options.bar.step * index + options.bar.topDis"></rect>
          <text v-if="options.numUnit.isShow"
          :font-size = "options.numUnit.fontSize"
          :fill = "options.numUnit.color"
          :transform = "groups (index, options.numUnit.leftDis, options.numUnit.topDis, options.numUnit.step)">
          {{ options.numUnit.unitName }}
          </text>
        </g>
      </svg>
       <!--// 创建名称-->
      <div v-for="(d, index) in data"
          :key="bindKey(6, index)"
          :style = "{
            'font-size': options.text.textName.fontSize + 'px',
            'position': 'absolute',
            'width': options.text.textName.textNameWidth + 'px',
            'height': options.text.textName.textNameHeight + 'px',
            'color': nameColor(index),
            'font-family': options.text.textName.fontFamily,
            'left': options.text.textName.leftDis + 'px',
            'top': options.text.textName.topStep * index +
                    options.text.textName.topDis +
                    'px'
          }"
          >{{ d.name }}</div>
      <!--// 创建数值-->
      <div v-for="(d, index) in data"
          :key="bindKey(7, index)"
          :style = "{
            'font-size': options.text.textNum.fontSize + 'px',
            'position': 'absolute',
            'width': options.text.textNum.textNumWidth + 'px',
            'height': options.text.textNum.textNumHeight + 'px',
            'color': numColor(index),
            'font-family': options.text.textNum.fontFamily,
            'left': options.text.textNum.leftDis + 'px',
            'top': options.text.textNum.topStep * index +
                    options.text.textNum.topDis +
                    'px',
            'text-align': 'left',
            'overflow': 'hidden',
            'white-space': 'nowrap',
            'text-overflow': 'ellipsis'
          }"
          ><tooltip :val="numOrPercent_value(d)">{{ numOrPercent_value(d) }}</tooltip></div>
      <!--// 创建类别名称-->
      <div v-if="options.categoryName.isShow"
          :style = "{
            'font-size': options.categoryName.fontSize + 'px',
            'position': 'absolute',
            'width': options.categoryName.textNumWidth + 'px',
            'height': options.categoryName.textNumHeight + 'px',
            'color': options.categoryName.color,
            'font-family': options.categoryName.fontFamily,
            'left': options.categoryName.leftDis + 'px',
            'top': options.categoryName.topDis + 'px'
          }"
          >{{ options.categoryName.name }}</div>
  </div>
</template>

<script>
import * as d3 from 'd3'
import $ from 'jquery'
import TWEEN from 'tween.js'
import tooltip from './../svt-common/tooltip.vue'
// 继承父元素属性
const props = {
  // 组件宽
  width: {
    type: Number,
    default: window.innerWidth
  },
  // 组件高
  height: {
    type: Number,
    default: window.innerHeight
  },
  // 组件数据
  data: {
    type: Array,
    default: () => []
  },
  // 组件配置项
  options: {
    // 标志位
    mark: 'resource-item',
    // 是否需要排序
    sort: true,
    // {HTMLDom} 执行父元素element
    element: '',
    // 次数单位
    numUnit: {
      isShow: false,
      unitName: '个',
      topDis: 13,
      leftDis: 786,
      step: 52.5,
      fontSize: 12,
      color: '#a0b6ff',
      fontFamily: 'GB-W3',
      difUnit: {
        isShow: true,
        unitName: ['TB', 'GB', 'MBPS']
      }
      //    textAnchor: 'start'
    },
    // 类别名称
    categoryName: {
      // true显示，false不显示
      isShow: true,
      name: '类别名称',
      fontSize: 20,
      textNumWidth: 100,
      textNameHeight: 40,
      color: '#a0b6ff',
      leftDis: 339,
      topDis: -48,
      fontFamily: 'GB-W3'
    },
    // 模块中文字的设置
    text: {
      // 模块中条形图的名称字符
      textName: {
        // 是否显示悬浮窗
        lab: {
          isShow: false
        },
        // 设置长度
        textNameWidth: 90,
        // 设置高度
        textNameHeight: 40,
        // 背景颜色
        bg_color: 'rgba(33,117,255,0.1)',
        lineHeight: 40,
        // 一列数据情况
        // 字符与左侧的边距
        leftDis: 320,
        // 字符与上方的边距
        topDis: -5,
        // 字符横向间距
        leftStep: 40,
        // 字符纵向间距
        topStep: 52,
        // 字符的大小
        fontSize: 16,
        // 字符的颜色
        color: '#a0b6ff',
        otherColor: {
          // 是否需要
          isShow: true,
          // 不同填充色情况
          color: [
            '#ff3c00',
            '#ffc000',
            '#57d2fb',
            '#08d1af',
            '#f66b68',
            '#5769fb',
            '#a361d6',
            '#8da4ff',
            '#37c43b',
            '#55acee'
          ],
          // 不同填充色的条数
          otherColorNum: 3
        },
        // 对齐方式  start middle end
        textAnchor: 'middle',
        textAlign: 'center',
        // 显示的字符串长度
        textLength: 100,
        // 两列数据情况
        // 字符与左侧的边距
        leftDis2: 22,
        // 字符与上方的边距
        topDis2: 45,
        // 字体
        fontFamily: 'GB-W3'
      },
      // 模块中条形图显示的数量字符
      textNum: {
        // 是否显示悬浮窗
        lab: {
          isShow: true
        },
        // 设置长度
        textNumWidth: 100,
        // 设置字符对齐方式
        textAlign: 'end',
        // 一列数据情况
        // 字符与左侧的边距
        leftDis: 730,
        // 字符与上方的边距
        topDis: 0,
        // 位置是否统一情况
        same: true,
        // 字符横向间距
        leftStep: 0,
        // 字符纵向间距
        topStep: 52,
        // 字符的大小
        fontSize: 14,
        // 字符的颜色
        color: '#a0b6ff',
        otherColor: {
          // 是否需要
          isShow: true,
          // 不同填充色情况
          color: [
            '#ff3c00',
            '#ffc000',
            '#57d2fb',
            '#08d1af',
            '#f66b68',
            '#5769fb',
            '#a361d6',
            '#8da4ff',
            '#37c43b',
            '#55acee'
          ],
          // 不同填充色的条数
          otherColorNum: 3
        },
        // 对齐方式  start middle end
        textAnchor: 'end',
        // 显示的字符串长度
        textLength: 20,
        // 两列数据情况
        // 字符与左侧的边距
        leftDis2: 455,
        // 字符与上方的边距
        topDis2: 59,
        // 字体
        fontFamily: 'SQUARE721'
      }
    },
    // 模块中条形图的设置
    bar: {
      // 条形图距离模块顶部的距离
      topDis: 0,
      // 条形图距离模块左边的距离
      leftDis: 425,
      // 位置是否统一情况
      same: true,
      // 条形图之间的间隔
      step: 53,
      // 条形图的高度
      barHeight: 10,
      // 条形图的宽度
      barWidth: 285,
      // 圆角矩形rx值
      barRx: 5,
      // 圆角矩形ry值
      barRy: 10,
      // 填充颜色
      color: '#3fabfc',
      otherColor: {
        // 是否需要
        isShow: true,
        // 不同填充色情况
        color: [
          '#ff3c00',
          '#ffc000',
          '#57d2fb',
          '#08d1af',
          '#f66b68',
          '#5769fb',
          '#a361d6',
          '#8da4ff',
          '#37c43b',
          '#55acee'
        ],
        // 不同填充色的条数
        otherColorNum: 3
      }
    },
    // 条形图背景图设置
    bar_bg: {
      // 填充颜色
      color: 'rgba(255, 255, 255, .1)'
    },
    // 条形图按照百分比大小绘制
    percentName: {
      isShow: false
    },
    // 分段条形图
    section_bar: {
      // 是否显示
      isShow: false,
      // 每段宽度
      section_bar_width: 8,
      // 偏移量
      x: 130,
      y: 30,
      // 小矩形数量
      section_bar_num: 32,
      // 间隔大小
      section_bar_space: 1,
      step: 0
    },
    // 分段条形图背景图
    section_bar_bg: {
      // 是否显示
      isShow: true,
      // 每段宽度
      section_bar_bg_width: 8,
      // 间隔大小
      section_bar_bg_space: 1
    },
    // 数据列数
    columnNum: 1,
    data: []
  }
}
export default {
  name: 'svt-rank',
  props,
  components: {
    tooltip
  },
  data () {
    return {
      realWidth: []
    }
  },
  // 计算变量
  computed: {
    // 深拷贝后的配置项
    _options () {
      let options = $.extend(true, {}, props.options.default, this.options)
      return options
    },
    max () {
      let max = this.data[0].num
      for (let i = 0; i < this.data.length; i++) {
        if (max < this.data[i].num) max = this.data[i].num
      }
      return max
    },
    barWidth () {
      let barWidth = []
      for (let i = 0; i < this.data.length; i++) {
        if (this.data[i].num / this.max <= 0.01 && this.data[i].num !== 0) {
          barWidth.push(3)
        } else {
          barWidth.push(this.data[i].num / this.max * this.options.bar.barWidth)
        }
      }
      return barWidth
    }
  },
  methods: {
    // 条形图偏移量
    groups (index, leftDis, topDis, step) {
      return `translate(${leftDis}, ${topDis + step * index})`
    },
    // 绑定不重复值
    bindKey (index1, index2) {
      return `${index1}-${index2}`
    },
    // 条形图颜色
    barColor (index) {
      if (this.options.bar.otherColor.isShow) {
        return this.options.bar.otherColor.color[index]
      } else {
        return this.options.bar.color
      }
    },
    // 名称颜色
    nameColor (index) {
      if (this.options.text.textName.otherColor.isShow) {
        return this.options.text.textName.otherColor.color[index]
      } else {
        return this.options.text.textName.color
      }
    },
    // 数值颜色
    numColor (index) {
      if (this.options.text.textNum.otherColor.isShow) {
        return this.options.text.textNum.otherColor.color[index]
      } else {
        return this.options.text.textNum.color
      }
    },
    // 分段条形图总数
    sectionBarNum (index) {
      let sectionBarNum = []
      for (let i = 0; i < this.data.length; i++) {
        sectionBarNum.push(Math.round(this.data[i].num * this.options.section_bar.section_bar_num / this.max))
      }
      return sectionBarNum[index]
    },
    // 分段条形图总数
    sectionBarBgNum () {
      return this.options.section_bar.section_bar_num
    },
    // 显示数值或百分比
    numOrPercent_value (d) {
      if (this.options.percentName !== undefined && this.options.percentName.isShow) {
        return d.percent
      } else {
        return d.num
      }
    },
    // 根据百分比显示宽度
    numOrPercent_barWidth () {
      let percentLinear = d3.scaleLinear().domain([0, 100]).range([0, this.options.bar.barWidth])
      let percentName = []
      if (this.options.percentName !== undefined && this.options.percentName.isShow) {
        for (let i = 0; i < this.data.length; i++) {
          percentName.push(percentLinear(Number(this.data.percent.substring(0, this.data.percent.length - 1))))
        }
      }
    }
  },
  watch: {
    barWidth () {
      let _self = this
      function animate () {
        if (TWEEN.update()) {
          requestAnimationFrame(animate)
        }
      }
      this.barWidth.forEach((d, i) => {
        new TWEEN.Tween({width: 0})
          .to({width: d}, 1000)
          .onUpdate(function () {
            _self.$set(_self.realWidth, i, this.width)
          })
          .start()
      })
      animate()
    }
  }
}
</script>



